	module.exports = {"title":"First Post","date":"2006-07-22T22:39:53.000Z","layout":"post","body":"<p>A small confession on my part, I love reading blogs. Ive been reading blogs for quite a while now, five years Id suppose. It has been my privilege during this time to read the discussions and thoughts of hundreds of different intelligent and opinionated blog authors from all around the earth. Im in college now, this fall will be my Junior year. Everyday I listen to lecture and read from textbooks and most dont hold a candle to my daily dose of blog news and analysis. The blog is the greatest conversational tool ever invented because seconds after I click publish for this post, it can be read across the world by thousands. Granted, this blog, along with most of the other <a href=\"http://www.blogherald.com/2005/10/10/the-blog-herald-blog-count-october-2005/\">100 million</a> blogs in existence, wont be read much. But thats all right. Conversation is fun, even its just with a few of your buds. Im just happy to join the party.</p>\n"}

