	module.exports = {"title":"Why I blogPart 1","tags":["Learning","Writing"],"date":"2006-08-12T22:52:44.000Z","layout":"post","body":"<p>Id been thinking about blogging for a long time but what finally pushed me over the edge was this <a href=\"http://www.insidehighered.com/views/2005/11/22/mclemee\">post</a>. The author, Scott McLemee, wrote about what hes learned from a pamphlet by C. Wright Mill On Intellectual Craftsmanship. Heres the key quote,</p>\n<blockquote>\n<p>What Mills calls intellectual craftsmanship involves more than the ability to produce work that can pass peer review. Scholarship is a choice of how to live, he writes, as well as a choice of career. It is (if I may be excused for borrowing another <a href=\"http://www.hnn.us/blogs/entries/18349.html\">old Greek word</a>) an <em>ethos.</em> That is, a structure of habits that sustains and embodies a quality of mind, a tendency of character.</p>\n<p>Whether he knows it or not, Mills goes on to say, the intellectual workman forms his own self as he works towards the perfection of his craft. The notion of having a career is subordinate to  even a side-effect of  this process of inner shaping. To realize his own potentialities, and any opportunities that come his way, the scholar constructs a character which has its core the qualities of the good workman.</p>\n<p>For Mills, there is a kind of bench where all of this crafting takes place. He calls it the file. Im not sure this is the happiest of expressions. Its simple enough, but Mills uses it in his own sense.&quot;</p>\n</blockquote>\n<p>Mills suggests that aspiring young intellectuals keep what he calls, the file, or the term I like, intellectual journal. Here are collected reading notes, stray ideas, and complications or successes in research. It serves as a journal as you analyze and sort through ideas. Mill writes that the process of collecting and sifting through your notes and ideas leads to more systematic thinking as well as more directed and thorough learning.</p>\n<p>Starting my junior and senior years of high school, I came to love writing. Not that it is entertaining or easy  its about the hardest thing to do intellectually  but for the powerful and beneficial effect it has on my thinking. I find that when I write about an idea Ive had or a book Ive read, the writing serves to clarify my thinking and make explicit my reasoning.</p>\n<p>My experience with the effects of writing fits with Mills main assertion, that the keeping of the file or intellectual journal is critical to the [construction of] a character which has its core the qualities of the good workman. My writing refined and shaped my intellectual charecter.</p>\n<p>A critical realization Ive come to over the past several years is the importance of my habits. My life is directed more by what I choose to do every day then any so-called life changing decisions. I like to think of habits as bricks. A single brick is really quite insignificant but from thousands of bricks mortared together come sturdy houses and soaring cathedrals. And from habits we build, one brick at a time, our lives. My life, whether it will be useful and great or broken and poor, depends a great deal on my habits.</p>\n<p>The great disconnect in most peoples lives is between what they know they should do and what they actually do. Witness the vast number of people enslaved by addictions: alcohol, drugs, gambling, food, and so forth. What they lack is internal discipline or the character to live what they know is right. For whatever reason they have never built the character that allows their mind to rule their body. Instead, their bodies enslave their minds.</p>\n<p>So how does this bring us back to Mills file? For many years, there has been a disconnect between what I want to be intellectually, and what I actually do, my habits. I feel far too often I dont think as I should. I dont analysis, probe, collect, sift, sort, synthesize. In other words, as much as I want to, I dont have the character of an intellectual workman.</p>\n<p>My hope is this blog will provide the motivation and discipline to write so as to help me develop the habits of the mind of an intellectual craftsman.</p>\n<p><strong>Update:</strong> Other people seem to be thinking along the same lines: <a href=\"http://savageminds.org/2006/08/14/c-wright-mills-on-blogging/\">here</a> and <a href=\"http://www.aaronsw.com/weblog/about\">here</a>. Also, <a href=\"http://ddl.uwinnipeg.ca/res_des/files/readings/cwmills-intel_craft.pdf\">here</a> is a link to a PDF of Mills orginal essay, On Intellectual Craftsmanship.<a href=\"http://savageminds.org/2006/08/14/c-wright-mills-on-blogging/\"></a></p>\n"}

