	module.exports = {"title":"A Good Test","tags":["Education","Learning"],"date":"2006-12-31T06:37:26.000Z","layout":"post","body":"<p>I recently finished up a semester at <a href=\"http://www.byu.edu\" title=\"BYU\">BYU</a>. The end of another semester brings with it that joyful tradition, finals. Going through six finals gave me cause to think quite a bit about tests; what are they for and why are they useful?</p>\n<p>I came up with three ways to measure the quality of a test:</p>\n<ol>\n<li>Does it measure something valuable?</li>\n<li>Is the test both reliable and valid?</li>\n<li>Does it provide the correct incentives?</li>\n</ol>\n<p><strong>Does it measure something valuable?</strong><br>\nThis depends on the context of the test  a juggling test is valuable for a circus school, a gum chewing test is valuable to aspiring gum gurus, etc. The important questions is, does the test measure ones understanding of or skill in the valuable part(s) of a subject matter.</p>\n<p><strong>Is the test both reliable and valid?</strong><br>\nReliable means the test is consistently administered and graded for all test takers. Valid means the test measures what it intends to. A juggling test which asks a student to hold two balls but not throw them would be a reliable test  all test takers could be measured easily the same  but wouldnt be valid as being able to hold two balls doesnt show that one can in fact juggle balls. A professor can prepare a test that measures all students fairly but wouldnt test the students on anything valuable (as defined above).</p>\n<p><strong>Does it provide the correct incentives?</strong><br>\nI havent worked this measure out satisfactorily in my mind yet but its something like the following. Peoples behavior is shaped by incentives. Because tests are a major part of a class grade and most students care about grades, tests motivate students to study more then they would without the incentive. So far this is fine and dandy. I for one appreciate tests for this very reason as I know Ive studied more, and learned more, over the years because of tests.</p>\n<p><strong>So why can this go wrong?</strong><br>\nStudents go to school to learn, presumably. There is of course the old yarn that education is the only thing that Americans pay for and hope to be cheated. Many students it seems care more about the grade then for learning. Because learning is hard, there is a strong incentive for students to try to game the test. Some types of tests can be passed by cramming the night before the tests. Tests that only require you to memorize material dont promote true learning. So this poorer sort of test would provide the wrong incentive to students in that it pushes them toward the poorer sort of learning. A good test would push students toward the richer, and harder, sort of learning.</p>\n<p>Now what types of tests provide the correct or wrong incentives Im not quite sure. As I said, I havent completely worked this measure out. Im not even sure if it is a correct measure. If at some point I can work up more interest in this topic to clarify my thinking, Ill write another post. Or someone else can save me the work and write it themselves (or link to a page in the comments section).</p>\n"}

