	module.exports = {"title":"Take Risks to Open Options","tags":["entrepreneurship","Learning"],"date":"2007-02-15T06:20:32.000Z","layout":"post","body":"<p>I was reading a <a href=\"http://ross.typepad.com/blog/2007/02/entrepreneur_hi.html\">blog post</a> earlier by <a href=\"http://ross.typepad.com/blog/\">Ross Mayfield.</a> He was asked what were his <a href=\"http://ross.typepad.com/blog/2007/02/entrepreneur_hi.html\">best decision and worst mistake as an entrepreneur.</a></p>\n<p>All his answers were good but I was especially struck by what he calls his biggest mistake:</p>\n<blockquote>\n<p><strong>Biggest Mistake  Not taking bigger risks earlier</strong></p>\n<p>Maybe because in hindsight all risks are clear, but I always find myself regretting not taking bigger risks earlier. For example, open sourcing the Socialtext code was something we waited on until the company had strong footing. Partially because we thought there would be cannibalization, partially because we were understaffed to really engage with the community. But I believe if we bought this bullet earlier in the history of the company we would be reaping better rewards. As a planning exercise, now I always try to ask two questions: How could we take more risk? and What risk can we take that creates the greatest amount of options? I find there is always a way to do a little more, in particular by getting past instinct to control prevalent in so many entrepreneurs.</p>\n</blockquote>\n<p>I was so struck because Im in the middle of taking a big risk.</p>\n<p>I am passionate about web development. I want to build web applications that enable people to connect, collaborate, and get more done in their lives. But at my current and soon to be past job, Im not learning how to build these beautiful web applications.</p>\n<p>So I decided to quit and employee myself. I decided that as no one is going to pay me to learn how to do web development, Ill just have to pay myself.</p>\n<p>And now my mom, older sister, and I are going into business together. In a few months, well unveil <a href=\"http://www.los-detalles.com\">Los-Detalles.com</a>, the newest and greatest baby card announcement e-retailer. And in the months leading up the our launch, I will be learning web development.</p>\n<p><a href=\"http://www.en.wikipedia.org/wiki/Ross_Mayfield\">Ross Mayfield</a> learned it was a mistake to not take risks but that taking (calculated) risks opens up options where caution and control can never go. Im trying to learn this same lesson. By taking this risk, the options <em>I</em> want to have will open to me.</p>\n"}

