	module.exports = {"title":"I really liked these insights","tags":["Education","entrepreneurship","innovation","Learning","Uncategorized"],"date":"2007-12-31T21:25:54.000Z","layout":"post","body":"<p>From the <a href=\"http://www.economist.com/business/displaystory.cfm?story_id=10328123\">Economist:</a></p>\n<blockquote>\n<p>At some point in the decade after he moved from the farm in Nebraska where he grew up to the innovation hub that is the San Francisco Bay Area, Evan Williams accidentally stumbled upon three insights:</p>\n<ol>\n<li>that genuinely new ideas are, well, accidentally stumbled upon rather than sought out</li>\n<li>second, that new ideas are by definition hard to explain to others, because words can express only what is already known</li>\n<li>and third, that good ideas seem obvious in retrospect.</li>\n</ol>\n</blockquote>\n"}

