	module.exports = {"title":"Assorted Links","tags":["blogging","elearning2.0","enterprise2.0","innovation"],"date":"2008-04-17T18:43:26.000Z","layout":"post","body":"<p>I think Im going to start doing link posts more often. I run into content I think I should write about here but then never have time to write a full-blown post. Onto the links.</p>\n<p><a href=\"http://www.boingboing.net/2007/06/21/clay-shirky-defends-.html\">Clay Shirky says Internet reduces needs for experts by lowering transaction costs</a></p>\n<blockquote>\n<p>Experts the world over have been shocked to discover that they were consulted not as a direct result of their expertise, but often as a secondary effect  the apparatus of credentialing made finding experts easier than finding amateurs, even when the amateurs knew the same things as the experts.</p>\n</blockquote>\n<p><a href=\"http://www.classroom20.com/profiles/blog/show?id=649749%3ABlogPost%3A115854\">Web 2.0 Is the Future of Education</a></p>\n<p>Lists 10 cultural trends which is pushing education towards a web2.0 model</p>\n<p><a href=\"http://www.elearnspace.org/blog/archives/003289.html\">A world without courses</a></p>\n<p>Thought experiment how universities would work without actual courses. An interesting ideas. Ive often wandered if courses are the best method for learning. I know I learn far more outside of class then inside the classroom.</p>\n<p><a href=\"http://tip.psychology.org/vygotsky.html\">Vygotskys Social Development Theory</a>  <a href=\"http://www.simplypsychology.pwp.blueyonder.co.uk/vygotsky.html\">more here</a></p>\n<blockquote>\n<p>Every function in the childs cultural development appears twice: first, on the social level, and later, on the individual level; first, between people (interpsychological) and then inside the child (intrapsychological). This applies equally to voluntary attention, to logical memory, and to the formation of concepts. All the higher functions originate as actual relationships between individuals.</p>\n</blockquote>\n<p><a href=\"http://freakonomics.blogs.nytimes.com/2008/03/11/how-did-a-rod-get-so-good/\">How to become great?</a> Research suggests:</p>\n<ol>\n<li>Focus on technique as opposed to outcome.</li>\n<li>Set specific goals.</li>\n<li>Get good, prompt feedback, and use it.</li>\n</ol>\n<p><a href=\"http://willrichardson.wikispaces.com/page/diff/home/20048645\">Thoughts, quotes, questions about how web2.0 is challenging traditional methods of education</a></p>\n<p><a href=\"http://tip.psychology.org/bandura.html\">Bandura Social Learning Theory</a></p>\n<blockquote>\n<p>Learning would be exceedingly laborious, not to mention hazardous, if people had to rely solely on the effects of their own actions to inform them what to do. Fortunately, most human behavior is learned observationally through modeling: from observing others one forms an idea of how new behaviors are performed, and on later occasions this coded information serves as a guide for action.</p>\n</blockquote>\n<p><a href=\"http://tip.psychology.org/lave.html\">Laves Situated Learning Theory</a></p>\n<blockquote>\n<p>Lave argues that learning as it normally occurs is a function of the activity, context and culture in which it occurs (i.e., it is situated). This contrasts with most classroom learning activities which involve knowledge which is abstract and out of context. Social interaction is a critical component of situated learning  learners become involved in a community of practice which embodies certain beliefs and behaviors to be acquired. As the beginner or newcomer moves from the periphery of this community to its center, they become more active and engaged within the culture and hence assume the role of expert or old-timer. Furthermore, situated learning is usually unintentional rather than deliberate. These ideas are what Lave &amp; Wenger (1991) call the process of legitimate peripheral participation.</p>\n</blockquote>\n<p><a href=\"http://www.socialtext.net/stanfordsocialmedia/index.cgi?syllabus\">Syllabus for Virtual community/social media class at Stanford</a>  Loads of links to great resources.</p>\n<p><a href=\"http://www.cognitive-edge.com/2006/10/they_did_not_respect_or_sit_st.php\">Learn the difference between states of order and unorder  a birthday story</a></p>\n<p>One of the most profound insights into management Ive read. To manage states of unorder you make boundaries, create attractors, stabilize the patterns you desire and disrupt the patterns that threaten danger and harm. Read the story linked above then the full paper <a href=\"http://www.cognitive-edge.com/articledetails.php?articleid=40\">here</a></p>\n<p><a href=\"http://www.cognitive-edge.com/blogs/guest/2008/04/on_average_averages_are_the_ex.php#more\">On average, averages are the exception not the rule</a></p>\n<p>Worth linking to for the title alone. Explores some failures of statistics particularly social scientists ignorance of Zipth or the Power law</p>\n<p><a href=\"http://sethgodin.typepad.com/seths_blog/2008/03/why-bother-havi.html\">Why bother having a resume?</a></p>\n<p>Seth Godin asks why bother having a resume. He says &quot;I think if youre remarkable, amazing or just plain spectacular, you probably shouldnt have a resume at all. . . Great jobs, world class jobs, jobs people kill for those jobs dont get filled by people emailing in resumes. Ever.</p>\n<p><a href=\"http://cannell.org/blog/?q=node/229\">Ross Mayfield Shares Four Use Cases For Wikis</a></p>\n<p>The four are:</p>\n<ol>\n<li>collaborative intelligence</li>\n<li>participatory knowledge base</li>\n<li>flexible client collaboration</li>\n<li>business social networks</li>\n</ol>\n<p><a href=\"http://www.technologyreview.com/blog/boyden/21925/\">How to think: Managing brain resources in an age of complexity</a></p>\n<p>Great, great suggestions on how to think. Read the whole thing. One sample point:</p>\n<blockquote>\n<p><strong>Make your mistakes quickly.</strong> You may mess things up on the first try, but do it fast, and then move on. Document what led to the error so that you learn what to recognize, and then move on. Get the mistakes out of the way. As Shakespeare put it, &quot;Our doubts are traitors, and make us lose the good we oft might win, by fearing to attempt.</p>\n</blockquote>\n<p><a href=\"http://www.wikipatterns.com/display/wikipatterns/Wikipatterns\">Wikipatterns  Design patterns for wikis</a>  Great resource</p>\n<p><a href=\"http://blog.hbs.edu/faculty/amcafee/index.php/faculty_amcafee_v3/now_thats_what_im_talking_about/\">Case study of Enterprise2.0 implementation</a> at <a href=\"http://www.avenuea-razorfish.com/\">Avenue A | Razorfish</a></p>\n<p><a href=\"http://www.shirky.com/writings/situated_software.html\">Situated Software</a></p>\n<p>Clay Shirky tells how open source software enables cheap creation of social software situated to needs of a specific community. Hes ideas are partly what inspired my creation of <a href=\"http://vinvesting.com/docs/munger/human_misjudgement.html\">BYU</a></p>\n<p><a href=\"http://vinvesting.com/docs/munger/human_misjudgement.html\">The Psychology of human misjudgement</a></p>\n<p>Psychology fascinates me  particularly how we mess up. My hope is if I understand why my psychology causes me to mess up, Ill be able to compensate. This is a speech by Charlie Munger, a wise old sage of the business world. He lists 24 reasons why humans misjudge.</p>\n<p><a href=\"http://avc.blogs.com/a_vc/2008/03/where-my-traffi.html\">Where my traffic comes from</a></p>\n<p>Popular blogger says more traffic now comes from &quot;smart aggregators. As there is more and more good content being posted, people are starting to rely more on social filtering.</p>\n<p><a href=\"http://acko.net/blog/safe-string-theory-for-the-web\">Safe String Theory for the Web</a>  Everything you ever wanted to know about handling strings on the web.</p>\n<p><a href=\"http://www.nytimes.com/2008/04/02/opinion/02aamodt.html?_r=3&amp;oref=slogin&amp;oref=slogin&amp;oref=login\">Willpower is like a muscle says research</a></p>\n<p>New York Times article summarizes research that suggests your will power can be strengthened by exercising it, much as a muscle.</p>\n<blockquote>\n<p>In psychological studies, even something as simple as using your nondominant hand to brush your teeth for two weeks can increase willpower capacity. People who stick to an exercise program for two months report reducing their impulsive spending, junk food intake, alcohol use and smoking. They also study more, watch less television and do more housework. Other forms of willpower training, like money-management classes, work as well.</p>\n</blockquote>\n"}

