	module.exports = {"title":"Excellence, creativity, innovation","date":"2008-05-17T15:12:59.000Z","draft":true,"layout":"post","body":"<p>Ive been thinking a lot lately about excellency. What it means to be excellent and how to get there. I havent come to any firm conclusions but I wanted to quote a number of resources that have contributed to my thinking blah blah blah</p>\n<p>How to educate yourself to do something excellent </p>\n<p>seth goodin  best jobs dont take resumes, Great jobs, world class jobs, jobs people kill for those jobs dont get filled by people emailing in resumes. Ever.</p>\n<p><a href=\"http://sethgodin.typepad.com/seths_blog/2008/03/why-bother-havi.html\">http://sethgodin.typepad.com/seths_blog/2008/03/why-bother-havi.html</a></p>\n<p>these people dont exist, anything innovative will get you pushback,</p>\n<p><a href=\"http://money.cnn.com/2008/04/29/magazines/fortune/larry_page_change_the_world.fortune/index3.htm\">http://money.cnn.com/2008/04/29/magazines/fortune/larry_page_change_the_world.fortune/index3.htm</a></p>\n<p>deliberate practice (from freckonomics report),</p>\n<p><a href=\"http://freakonomics.blogs.nytimes.com/2008/03/11/how-did-a-rod-get-so-good/\">http://freakonomics.blogs.nytimes.com/2008/03/11/how-did-a-rod-get-so-good/</a></p>\n<p>money in entrapreneurship comes in spurts --Steve Pavalina</p>\n<p>Confront the Brutal facts (yet never lose faith)</p>\n"}

