	module.exports = {"title":"Never update your resume again (just your blog)","tags":["blogging"],"date":"2008-05-17T13:22:14.000Z","layout":"post","body":"<p>Ive <a href=\"/why-i-blog-part-2/\">written before how your blog can replace your resume</a> but I enjoyed reading another bloggers experience. I havent had the chance yet to send my blog url instead of a resume but I have been contacted about a job once directly because of what Ive written here and elsewhere.</p>\n<blockquote>\n<p>I hated updating my resume. It is such a brilliantly inefficient medium to communicate your value proposition.</p>\n<p>Now when situations of resumes arise, I send the url for the blog. The longer it exists the more valuable it becomes as a alternative resume.</p>\n<p>On blogs you cant fake it (atleast not for a very long time) and it so perfectly reflects your intelligence, your character, your values, your smarts (or lack there of) and so on and so forth. You can fake the piece of paper, you cant fake a blog.</p>\n<p>Of course the flip side is also true. If you have a great blog you might not have to go look for a job. Theyll come find you. I am sure all the bloggers in our space get at least two job offers a week. :)</p>\n<p>And here is perhaps the nicest benefit of having your own blog (and making sure your potential new employer has it and has sent it to the interview committee): They wont ask you silly questions.</p>\n<p>They have a good idea of who you actually are and smart interviewers just get to the point. And that is a good thing.</p>\n</blockquote>\n<p><a href=\"http://www.kaushik.net/avinash/2008/05/benefits-of-blogging-a-practitioners-perspective.html\">Read the whole thing.</a></p>\n"}

