	module.exports = {"title":"Memetracker module gets some press","tags":["memetracker"],"date":"2008-09-17T22:08:08.000Z","layout":"post","body":"<p>The <a href=\"http://drupal.org/project/memetracker\">Memetracker module</a> I worked on this past summer for Google Summer of Code saw some press today. <a href=\"http://webdevnews.net/2008/09/memetracker-module-released-for-drupal/\">Web Dev News wrote</a>:</p>\n<blockquote>\n<p>One of the most powerful and innovative modules to be developed for Drupal this year is Memetracker. Written by Kyle Mathews in the Google Summer of Code 2008, Memetracker intelligently filters content on a website to synthesize the most popular and important topics and conversations into memes. Similar to the way Google News and TechMeme operate, Memetracker can work with RSS and Atom feeds to track issues, current events, and internet buzz across numerous channels simultaneously. The module is a smart aggregator that can analyze the actual text of RSS feeds and group posts with related content into categories that can be displayed on a website.</p>\n</blockquote>\n<p><a href=\"http://webdevnews.net/2008/09/memetracker-module-released-for-drupal/\">Read the rest.</a></p>\n<p>I still find it rather weird to see my name in print (digital or otherwise) but its nice to see the module getting some notice. Drupal and memetracking are a powerful combination for building better communication tools for communities.</p>\n<p>All developers out there  memetracker is still in alpha and could use your help. If youd like to help out on development, please <a href=\"/contact\">contact me</a>.</p>\n"}

