	module.exports = {"title":"What are Social Objects?","tags":["social software","drupal planet","social objects"],"date":"2009-02-22T01:48:22.000Z","layout":"post","readNext":"/how-design-social-networking-site-using-social-objects/","body":"<p><em>In preparation for <a href=\"http://dc2009.drupalcon.org/session/building-advanced-social-networks-large-us-university.html\">my upcoming session at Drupalcon</a>, Im writing a short series of articles on social networking and learning theory. First up is an article on social objects.</em></p>\n<blockquote>\n<p>The Social Object, in a nutshell, is the reason two people are talking to each other, as opposed to talking to somebody else.\n <a href=\"http://www.gapingvoid.com/Moveable_Type/archives/004390.html\">Hugh Macleod</a></p>\n</blockquote>\n<p>As humans we like to socialize but in order to socialize, we need a reason to get together. Social objects provide that reason.</p>\n<h3>Some examples of social objects</h3>\n<p>You and John are coworkers on a team working on the launch of a new microsite for your company. Your company, team, and project are all social objects.</p>\n<p>You and Fred are both Information Systems majors and have become friends as you study for tests and do homework together. Your major, classes, and homework assignments are social objects.</p>\n<p>Your wife wants to throw a big birthday party for you so she can invite all of her and your friends. The birthday party is a social object.</p>\n<p>Sue holds a tea party every Sunday evening. She invites all her friends and they come as they can. The tea party is a social object.</p>\n<h3>Social objects bind us together.</h3>\n<p>The more important a social object is to us, the stronger itll bind us to others who also hold that social object. I love <a href=\"http://drupal.org\">Drupal</a> and spend a good part of my time using it to build <a href=\"https://island.byu.edu\">cool websites</a>. As I meet others who also loves Drupal, we immediately have a strong connection through that shared social object.</p>\n<p>When you meet someone new or go on a first date, you start asking questions hoping to find something in common or shared social objects.</p>\n<p>If you date someone for a few months then break up, the reason youll often give is, we just didnt have much in common.</p>\n<p>Families develop traditions. My family eats fancy sandwiches each Christmas Eve. The shared memories and rituals weve developed bind us together. Traditions are social objects.</p>\n<h3>Social objects come in all sorts of types and strength</h3>\n<p><a href=\"http://www.zengestrom.com/blog/2007/08/what-makes-a-go.html\">Good social objects</a> are complex and have lots of hooks around which to start conversations. A football game has history, statistics, personalities, rivalries, and a plot with heroes, goats, and momentum shifts. All of which provide rich meat for discussion before, during and after the game.</p>\n<h3>We change our behavior because of social objects</h3>\n<p>If your coworkers are NBA fans and theres a big game one night, youll watch the game so you can talk about it with your coworkers the next day. You buy that silly new iPhone app so you can share it with your coworkers. You join Facebook because all your friends are there. The NBA game, your new iPhone app, and Facebook are all social objects.</p>\n<p>But its important to remember that its not the social objects that matter but people. Loving and being loved is what matters. Social objects are just the tool we use to make it happen.</p>\n<p>Next Ill write about <a href=\"/how-design-social-networking-site-using-social-objects\">how social object theory can help us build better social networking websites</a>.</p>\n"}

