	module.exports = {"title":"How to design a social networking site using social objects","tags":["social software","drupal planet","social objects","social networking"],"date":"2009-02-27T18:20:59.000Z","layout":"post","readNext":"/what-are-social-objects/","body":"<p>This is the second in a series of articles Im writing to help me prep for my <a href=\"http://dc2009.drupalcon.org/session/building-advanced-social-networks-large-us-university\">session next week at Drupalcon</a>. The first article was my attempt to <a href=\"/what-are-social-objects\">define social objects</a>. This article outlines how to use social object theory to design better social networking sites.</p>\n<p>First a review of <a href=\"http://www.gapingvoid.com/Moveable_Type/archives/004390.html\">what are social objects</a>. The Social Object is the reason two people are talking to each other, as opposed to talking to somebody else. People are social creatures. Social objects are the tools we use to create opportunities to socialize with other people. And lastly, for all their importance, objects are just a means to an end  the end is loving and being loved.</p>\n<p>So social objects are a cool theory but how can they help me design my new social networking site (or fix my broken one)?</p>\n<p>Jyri Engestrom, founder of <a href=\"http://jaiku.com/\">Jaiku</a>, answered this question in his blog post, <a href=\"http://www.zengestrom.com/blog/2005/04/why_some_social.html\">Why some social network services work and others dont  Or: the case for object-centered sociality.</a></p>\n<p>He argues:</p>\n<blockquote>\n<p>the term social networking makes little sense if we leave out the objects that mediate the ties between people. Think about the object as the reason why people affiliate with each specific other and not just anyone. For instance, if the object is a job, it will connect me to one set of people whereas a date will link me to a radically different group. This is common sense but unfortunately its not included in the image of the network diagram that most people imagine when they hear the term social network. The fallacy is to think that social networks are just made up of people. Theyre not; social networks consist of people who are connected by a shared object.</p>\n<p>The social networking services that really work are the ones that are built around objects. And, in my experience, their developers intuitively get the object-centered sociality way of thinking about social life. Flickr, for example, has turned photos into objects of sociality. On <a href=\"http://del.icio.us\">del.icio.us</a> the objects are the URLs. EVDB, <a href=\"http://Upcoming.org\">Upcoming.org</a>, and evnt focus on events as objects.</p>\n</blockquote>\n<p>Jyri wrote further that networking sites not based on social objects tend to not work as they are built on a flawed model for human interaction.</p>\n<p>The steps then for designing your social networking site around social objects are first, pick the objects around which people will socialize, next, decide what users can do to the objects and finally, design how people can share the objects or other wise socialize around the objects. Or as <a href=\"http://twitter.com/chrismessina/statuses/1053693513\">Chris Messina</a> put it, define the objects, name the verbs, network the objects</p>\n<p>Lets look at some popular social networking sites and see how theyve built their sites around social objects.</p>\n<p><a href=\"http://flickr.com\">Flickrs</a> social objects are pictures. People can do the following to pictures (the verbs): comment, annotate, tag, add them to sets and to groups, and share pictures through their URL.</p>\n<p><a href=\"http://youtube.com\">YouTubes</a> social objects are videos. People can watch, comment, rank, favorite, flag, create playlists of, and share objects.</p>\n<p><a href=\"http://dogster.com\">Dogsters</a> social objects are dogs (obviously). People can create profiles (for their dogs), give gifts, adopt other dogs, post videos and pictures, and find dog related businesses near their homes.</p>\n<p><a href=\"http://drupal.org\">Drupals</a> social objects are Drupal (the software), the various modules in contrib and core, documentation pages, individual issues, and the various larger subtopics within the community such as documentation, social networking, education, and every other group found at <a href=\"http://groups.drupal.org\">groups.drupal.org</a> (each group is a social object).</p>\n<p>Around issues in core and contrib, Drupalers can write issues, discuss issues on Twitter, IRC, email, blogs, enlist help on the issues through the same channels, and finally write code to solve issues.</p>\n<p>Drupal, the social object, brings people to Drupalcon and various other meetups around the world. Drupal draws us to read Drupal planet and comment on peoples blogs. Drupal draws us to <a href=\"http://groups.drupal.org/node/19480\">comment and support other developers</a> building sites with Drupal.</p>\n<p><a href=\"http://horncologne.com/content/community_our_strength_drupal_epiphany\">As</a> <a href=\"http://www.matthewbull.net/2008/10/23/hooked-into-drupal/\">is</a> <a href=\"http://www.angrydonuts.com/i-was-voted-packt-publishing-drupal-most-valuable-player\">often</a> <a href=\"http://cmscritic.com/drupal-founder-dries-buytaert-interview\">mentioned</a>, the strength of Drupal is not the code but the community. The ideals and goals of Drupal (the idea not software) is what draws more and more people to the Drupal community. And as positive interaction builds upon positive interaction, the social object Drupal becomes more and more powerful.</p>\n<p>Jyri commented upon this in <a href=\"http://www.youtube.com/watch?v=SiWjAVcWK4g\">a keynote he delivered last year</a>. There he showed a picture hed taken of himself and his boy in a forest. He said this picture had value but when he placed the picture on Flickr and his friends commented on it the picture became much more valuable.</p>\n<p>The picture had intrinsic value as an object but became much more valuable when it became a social object. The comments by Jyris friends turned his picture into a social object. Or as <a href=\"http://www.gapingvoid.com/Moveable_Type/archives/004318.html\">Hugh likes to put it</a>, social gestures beget social objects. And as social gestures accumulate, the more valuable the social object becomes.</p>\n<p>JP Rangaswami created, what I think, is a <a href=\"http://confusedofcalcutta.com/2008/02/16/musing-about-social-objects-molluscs-that-matter/\">brilliant metaphor for understanding how social objects grow through conversations</a>. He compared the growth in value of a social object to the gradual adding of new layers to a pearl by an oyster.</p>\n<p>He wrote:</p>\n<blockquote>\n<p>Conversations grow around social objects, much like pearls grow around microscopic dust. Social objects are about growth, they are live.</p>\n<p>A successful social object is one that has layer upon layer of conversation created around it; as the number of participants increases, social objects enjoy network effects.</p>\n</blockquote>\n<p>So to sum things up. To create a thriving social networking site, first choose good social objects for the core of your site and then create ways for people to share the social objects and socialize around them.</p>\n<p>My next post will be on social learning or how social object theory can help us design websites which help people with similar interests connect to each other and learn together.</p>\n"}

