	module.exports = {"title":"Ignite Salt Lake 2 Presentation","tags":["ignite","ignite salt lake","presentation"],"date":"2009-03-27T18:07:51.000Z","layout":"post","body":"<p>I had a great time speaking last night at <a href=\"http://www.ignitesaltlake.com/ignite/index.cfm\">Ignite Salt Lake 2</a> on what were doing down at BYU with social networking in the classroom. Thanks to all the organizers, sponsors, and other presenters!</p>\n<p>\n  <iframe src=\"http://www.slideshare.net/slideshow/embed_code/1211626\" width=\"427\" height=\"356\" frameborder=\"0\" marginwidth=\"0\" marginheight=\"0\" scrolling=\"no\" style=\"border:1px solid #CCC; border-width:1px 1px 0; margin-bottom:5px; max-width: 100%;\" allowfullscreen> </iframe>\n</p>\n"}

