	module.exports = {"title":"Video and pictures from my presentation at Ignite","tags":["social networking","ignite","ignite salt lake","social learning"],"date":"2009-04-04T00:44:22.000Z","layout":"post","body":"<p>Video from <a href=\"http://www.ignitesaltlake.com\">Ignite SLC 2</a> was posted earlier this week. You can <a href=\"http://www.ignitesaltlake.com/ignite/index.cfm/watch-the-presentations/\">watch all the videos at the Ignite SLC website</a>. Ive embedded below the video of my presentation on building social networks at universities.</p>\n<p>Also, check out some <a href=\"http://www.flickr.com/photos/calanan/3389617343/in/set-72157615984649932/\">pictures taken of me in the Ignite SLC Flickr photostream</a>.</p>\n"}

