	module.exports = {"title":"Deploying Social Software in Universities: Go Broad then Deep","tags":["Education","elearning2.0","enterprise2.0","Learning","social software","social learning"],"date":"2009-09-02T23:53:44.000Z","layout":"post","body":"<p>Michael Idinopolus wrote an intriguing post over on <a href=\"http://michaeli.typepad.com/my_weblog/\">his excellent blog</a> yesterday titled <a href=\"http://michaeli.typepad.com/my_weblog/2009/08/enterprise-20-skip-the-pilot.html\">Enterprise 2.0: Skip the Pilot.</a></p>\n<p>I thought Id repeat some of his arguments because it agrees nicely with an argument Ive been formulating lately regarding deployment strategy for social learning software within higher education.</p>\n<p>But first to his article:</p>\n<blockquote>\n<p>Theres an orthodoxy in Enterprise 2.0 circles about how youre supposed to run an implementation. The orthodoxy goes something like this: Start with small-scale pilots, define your business objectives, watch the pilots closely, evaluate their success, make a go/no-go decision.</p>\n<p>As far as I can tell its what everyone thinks. In fact, its what I used to think. Unfortunately, its dead wrong. The orthodoxy is wrong for a very simple reason: Size matters. By constraining the size of your pilot, you significantly alter the way your company can and will use the tools.</p>\n<p>Im not opposed to pilots for most enterprise IT solutions. Companies like to pilot new technologies with small populations before they roll them out enterprise-wide. That approach makes a lot of sense for transactional systems like order management, project management, purchasing, ERP, and so on. By piloting with a small group, you reduce implementation risk. You get a read on the value of the solution, and you get feedback which you can use to make modifications while those modifications are still relatively easy and inexpensive.</p>\n<p>But social software is different from traditional IT. Traditional IT enables individuals to carry out well-defined, highly standardized transactions. Users go into the system to process transactionsto transfer funds, purchase supplies, track inventory, etc. The nature of these transactions, and the systems ability to enable them, do not vary much according to the number of people using the system. Whether 100 people are entering orders or 10,000 people are entering orders, the transactions themselves doesnt really change. What that means is that a representative small-scale sample is an accurate predictor of adoption and value at full scale.</p>\n<p>But Enterprise 2.0 tools are different from traditional IT systems. Traditional IT enables <strong>transactions</strong>; Enterprise 2.0 enables <strong>interactions</strong>.</p>\n<p></p>\n<p>That doesnt mean small-scale Enterprise 2.0 pilots cant succeed. They can, and many do. But even when pilots succeed, they have limited ability to predict how the organization goes on to use the capabilities once they are rolled out enterprise-wide. Pilots typically fall into the lower left-hand corner of the <a href=\"http://michaeli.typepad.com/my_weblog/2009/04/the-social-software-value-matrix.html\">Social Software Value Matrix</a>: improve existing interactions within existing silos. That includes things like project team workspaces, departmental workspaces, and technical knowledgebases. When organizations really embrace Enterprise 2.0, however, they almost always play in multiple sections of the Value Matrix, launching solutions like collaborative intranets, ideation portals, private extranets, Those solutions, almost by definition, require scale.</p>\n<p>Scale is the oxygen that feeds collaboration. Thats why collaborative tools like Facebook, and Twitter have taken off so spectacularly on the public web: With over a billion people on the Internet, the opportunities for interpersonal interaction are unbelievably high.</p>\n</blockquote>\n<p>Ok, fair enough. Social software needs other people in order to work properly (imagine Facebook if it was just you there). What does that have to do with deploying a social learning platform at a university?</p>\n<p>Before I answer that question, let me step back and explain a bit about what I mean by a social learning platform.</p>\n<p>First its designed to support social learning meaning its designed to help/encourage/faciliate conversations and connections between like-minded learners as they pursue their personal learning goals.</p>\n<p>Second its a platform giving the software two qualities:</p>\n<ol>\n<li>It gives people the freedom and tools to build onto the platform  in the case of a social learning platform, people will primarily build learning communities.</li>\n<li><a href=\"http://andrewmcafee.org/2006/05/the_mechanisms_of_online_emergence/\">New patterns or structure will emerge within the system</a> that are unplanned and undirected</li>\n</ol>\n<p>The social learning platform should improve existing interactions and create new interactions within classrooms, within majors, and between multiple institutions such as nearby universities or members of the community (or in other words, create value in each of the quadrants within the <a href=\"http://michaeli.typepad.com/my_weblog/2009/04/the-social-software-value-matrix.html\">social software value matrix</a>).</p>\n<p>My best stab at creating this tool can be seen at <a href=\"https://island.byu.edu\">https://island.byu.edu</a>a tool Ive been building for some time with the help of many others. Island provides simple tools to improve existing interactions and enable new interactions within classrooms, across campus, and eventually will have tools that will allow learners from multiple campuses interact.</p>\n<p>So how should such a tool be deployed at a university?</p>\n<p>Michael Idinopolus answers that question in a second article <a href=\"http://michaeli.typepad.com/my_weblog/2009/09/launching-at-scale-.html\">Launch E2.0 Broad, Then Go Deep</a> where he references <a href=\"http://ross.typepad.com/blog/2006/04/power_law_of_pa.html\">Ross Mayfields power law of participation graph</a>.</p>\n<p><a href=\"http://michaeli.typepad.com/my_weblog/2009/09/launching-at-scale-.html\"><img src=\"./power-law-participation.jpg\" alt=\"\"></a></p>\n<p>The key insight contained in the graph is that for different ways of participating in a social software environment there are different thresholds for joining. Reading takes little skill and effort while editing a wiki takes comparatively considerable more effort and community knowledge.</p>\n<p>From this wed also expect that the higher the threshold to participate in a certain way, the fewer people will participate. Survey results prove that out. While <a href=\"http://www.harrisinteractive.com/harris_poll/index.asp?PID=879\">roughly 20-30% of Americans regularly read blogs</a>, only <a href=\"http://www.pewinternet.org/Commentary/2005/May/New-data-on-blogs-and-blogging.aspx\">6% of Americans have created a blog</a> (and probably considerably less regularly write updates).</p>\n<p>Ive seen the same sort of behavior on Island. I <a href=\"/power-law-graphs-from-classroom-community-website\">wrote a post on this subject last year</a> where I noticed a power law that emerged when I graphed the students participation in a number of different ways. I only posted two graphs in that post but the same relationship showed up across all types of participation. A few participated a great deal but most didnt.</p>\n<p>Returning to Michaels article:</p>\n<blockquote>\n<p>Historically, Enterprise 2.0 implementations have focused on collaborative tools [with] fairly high participation thresholds: blogs and wikis. Thats not by design, its by default. Until recently, those were the only Enterprise 2.0 tools that showed potential for high-value business use.</p>\n<p>Today, Enterprise 2.0 participation is a whole different game. At the low threshold end of the curve, we have low-engagement tools like social messaging (internal Twitter), social bookmarking. By leading your implementation with these low-threshold tools, you lower the risk of implementation while still launching at the scale required for success.</p>\n</blockquote>\n<p>He goes on to describe an enthusiastic response to a recent webinar on his companies internal twitter app <a href=\"http://www.socialtext.com/products/signals.php\">Signals</a>.</p>\n<blockquote>\n<p>Signals was not just a useful tool, it was also a stepping stone that helped participants move to the right on the Participation curve (see image above). As participants started to get the hang of Signals, many started to ask about Socialtexts other collaborative features: What are workspaces? How do I use the Dashboard? How do I look up an individual?</p>\n</blockquote>\n<p>From what Ive seen, most social software deployments in universities have preceded in a similar way to what Michael describes  deploy high-value learning tools such as blogs and wikis in a small-pilot, evaluate their success, and make a go/no-go decision.</p>\n<p>I think this is wrong for the same reasons Michael lists. First, a social learning platform needs large numbers of learners to work (Scale is the oxygen that feeds collaboration) and second, blogs and wikis are difficult tools to use  both the steepness of learning curve and the amount of time required to use them effectively. Relatively few students will ever use them to the extent that some of us wish.</p>\n<p>In addition, I think we (grizzled web-heads who readily see the value of social software tools such as wikis and blogs in the classroom) frequently underestimate how scary and confusing these tools are to most teachers. These higher-threshold tools  they dont get in general and definitely dont get in specific, that is, how to use them in their classroom.</p>\n<p>With Island weve focused on the bottom of the participation curve  enabling simple group-based conversations through email. For the past year weve been building and testing Islands infrastructure within the Information Systems major. Were now focused on achieving scale by inviting the rest of the BYU campus. Once weve achieved scale, well (to quote Michael again) deliver deep value by following up with local champions [deans, teachers, students] who want to invest time and effort in more robust, group-specific forms of collaboration.</p>\n<p>This, I think, is the correct sequence. Launch simply and broadly. Let students and teachers adjust to having a web-enabled learning environment. Then go deep by building into the social learning platform higher-value tools such as blogs, wikis, and other collaborative learning tools and workflows.</p>\n<p>Your thoughts?</p>\n"}

