	module.exports = {"title":"How to create custom ical exports by category using Drupal and the Views and Calendar modules","tags":["drupal","drupal planet","taxonomy","ical","views","ical export"],"date":"2009-10-06T15:32:34.000Z","layout":"post","body":"<p>Lately Ive been helping out a web development team at BYU develop a new calendaring system in Drupal to replace our <a href=\"http://byunews.byu.edu/calendar/\">existing all-in-one calendar</a>. Its an ambitious project thats leveraging quite a few different modules from the Drupalsphere.</p>\n<p>Some of the features include:</p>\n<ul>\n<li>Import ical feeds.</li>\n<li>Filter events for display by event category</li>\n<li>Download customized ical feeds by category (i.e. Student wants to import just sport and academic events into his personal calendar).</li>\n<li>Hovering on event titles exposes tooltip (were using <a href=\"http://www.lullabot.com/files/bt/bt-latest/DEMO/index.html\">Beautytips</a>) with full event info</li>\n</ul>\n<p>Heres a screenshot of the UI as of last night:</p>\n<p><img src=\"./tooltip.png\" alt=\"\"></p>\n<p>I wanted to blog about how we created our ical exports by category as I couldnt find any documentation about this and it doesnt work out of the box.</p>\n<p>The default view from Calendar includes an ical display. This, by default, filters ical feeds by date  e.g. only include events in the ical feed after a certain date. This works fine.</p>\n<p>The problem we ran into is how to filter the ical feed by taxonomy terms. In Views we created a taxonomy term filter and exposed that to users so they can created custom calendar views of different event categories. That worked wonderfully. The problem was that when you downloaded the ical feed from the filtered calendar view, it included all events, not just events from the categories youd selected.</p>\n<p>After examining the url for the ical feed we found the problem.</p>\n<p>The default ical URL thats generated looks like this:</p>\n<p><code>/aiocalendar/calendar/ical/2009-10?tid[2]=2&amp;tid[1]=1&amp;tid[3]=3&amp;tid[4]=4&amp;tid[5]=5&amp;tid[6]=6&amp;tid[7]=7</code></p>\n<p>Views takes each url segment separated by a forward slash as arguments. <code>calendar/ical</code> tells views to return an ical representation of the calendar. <code>2009-10</code> tells views to only include events from October 2009 onward. But the next bit, <code>?tid[2][2]=2&amp;tid[1][1]=1&amp;tid[3][3]=3&amp;tid[4][4]=4&amp;tid[5]=5&amp;tid[6][5]=6&amp;tid[7][6]=7</code>, a list of term IDs that were currently active on the calendar view, werent working at helping Views produced a filtered ical feed.</p>\n<p>Digging into the taxonomy term argument, I discovered it expected arguments in the form of <code>1+2+3</code> or <code>1,2,3</code>. <code>1+2+3</code> meaning Views would return events that had terms <code>1 OR 2 OR 3</code> and <code>1,2,3</code> meaning Views would return events that had terms <code>1 AND 2 AND 3</code>. For example, <code>aiocalendar/calendar/ical/2009-10/1+4+6</code> would return an ical feed of all events from October 2009 onward that had term ids of <code>1 or 4 or 6</code>.</p>\n<p>I jumped to the command line and tested my theory by using wget and some handcrafted urls and it worked! The ical feeds I was getting only included events from categories I included as arguments in the URL.</p>\n<p>Now the trick was to override the ical url generated to replace it with one in the correct format. I found the theme function in the Calendar module where the the ical feed icon was generated, copied that to my module, and rewrote the url using some regex kungfu (read more <a href=\"http://drupal.org/node/11811\">on overriding theme functions</a> in the <a href=\"http://drupal.org/handbooks\">Drupal handbook</a>).</p>\n<pre><code class=\"language-php\"><span class=\"hljs-meta\">&lt;?php</span>\n<span class=\"hljs-comment\">/*\n * Override theme_calendar_ical_icon() so we can override the url produced.\n */</span>\n<span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> <span class=\"hljs-title\">phptemplate_calendar_ical_icon</span><span class=\"hljs-params\">($url)</span> </span>{\n  <span class=\"hljs-comment\">// Pull out array of Term IDs from the url.</span>\n  $search = preg_match_all(<span class=\"hljs-string\">\"/tid\\[([0-9])*\\]/\"</span>, $url, $matches);\n  $tids = $matches[<span class=\"hljs-number\">1</span>];\n\n  <span class=\"hljs-comment\">// Grab url stem, we'll add the Term ID arguments back in a bit.</span>\n  $search = preg_match(<span class=\"hljs-string\">\"/(.*ical\\/)20[0-9]{2}-.*/\"</span>, $url, $url_stem);\n\n  $url = $url_stem[<span class=\"hljs-number\">1</span>];\n\n  <span class=\"hljs-comment\">// Views arguments treat a \"+\" between arguments as OR.</span>\n  <span class=\"hljs-comment\">// So we're crafting an argument that asks for all events which match one of the</span>\n  <span class=\"hljs-comment\">// term IDs.</span>\n  <span class=\"hljs-keyword\">foreach</span>($tids <span class=\"hljs-keyword\">as</span> $tid) {\n    $url .= $tid . <span class=\"hljs-string\">\"+\"</span>;\n  }\n\n  <span class=\"hljs-comment\">// Trim off extra +</span>\n  $url = trim($url, <span class=\"hljs-string\">\"+\"</span>);\n\n\n  <span class=\"hljs-keyword\">if</span> ($image = theme(<span class=\"hljs-string\">'image'</span>, drupal_get_path(<span class=\"hljs-string\">'module'</span>, <span class=\"hljs-string\">'date_api'</span>) .<span class=\"hljs-string\">'/images/ical16x16.gif'</span>, t(<span class=\"hljs-string\">'Add to calendar'</span>), t(<span class=\"hljs-string\">'Add to calendar'</span>))) {\n    <span class=\"hljs-keyword\">return</span> <span class=\"hljs-string\">'&lt;div style=\"text-align:right\"&gt;&lt;a href=\"'</span>. check_url($url) .<span class=\"hljs-string\">'\" class=\"ical-icon\" title=\"ical\"&gt;'</span>. $image .<span class=\"hljs-string\">'&lt;/a&gt;&lt;/div&gt;'</span>;\n  }\n}\n<span class=\"hljs-meta\">?&gt;</span>\n</code></pre>\n<p>Well be releasing our calendar, custom code and all, as a feature on BYUs own feature server once the new calendar is released to production. But you can grab a development copy of it now at <a href=\"https://island.byu.edu/content/first-upload-calendar-feature\">https://island.byu.edu/content/first-upload-calendar-feature</a></p>\n<p>The BYU Drupal community is collaborating to improve our calendar feature as it solves nicely a common need on the many department web sites deployed on campus. If youd like to join the <a href=\"https://island.byu.edu/group/drupal-calendar\">Drupal Calendar group</a> to participate, <a href=\"mailto:mathews.kyle@gmail.com\">contact me</a>, and Ill create an account for you on our groups site.</p>\n"}

