	module.exports = {"title":"How we ran a micro-lab course","tags":["Education","social learning","micro-labs","connectivism"],"date":"2009-10-30T16:26:04.000Z","layout":"post","body":"<p>This is the second part of a (most likely) three-part series of posts Im harvesting from a journal article Tim Olsen and I wrote earlier this year. You might want to read the first post for context, <a href=\"/organizing-university-learning-moving-beyond-classroom\">Organizing University Learning: Moving Beyond the Course to Micro-labs</a>, before continuing here.</p>\n<p>This post is taken from the section of the paper where we describe our experience running a micro-lab course (with <a href=\"http://warp.byu.edu/site/\">Conan Albrecht</a> as the head teacher) in the <a href=\"http://marriottschool.byu.edu/bsisys/\">Information Systems department</a> at <a href=\"http://byu.edu\">BYU</a>.</p>\n<p>First a review of the micro labs concept taken from the <a href=\"/organizing-university-learning-moving-beyond-classroom\">first post</a>:</p>\n<blockquote>\n<p>Micro-labs are small learning communities centered around answering a question, not mastering standardized content/skills</p>\n<p>Micro-labs are a proposed university course architecture which supports and incorporates web 2.0 informal learning principles, enabling students to entirely create their own curriculum with the goal of contributing all objects created by learning back to a learning community of practice, and an Internet audience. This course design seeks to harness both the students natural (intrinsic) desire to learn and the ease of access to knowledge created by advances in communication technologies.</p>\n</blockquote>\n<p>In our micro-lab course, the students were asked to do four projects through the course of the semester. Each project had four steps. Students 1) create and post on the class website a learning contract specifying what they wish to learn, their process for learning, and planned deliverables. This contract is 2) revised with feedback from the instructor and fellow students. Students 3) complete the proposed learning contract and 4) share the completed deliverables by posting them to the class website and delivering an ignite-style presentation to the class (and if youre curious, all learning contracts, project deliverables, and public discussion can be see on the <a href=\"https://island.byu.edu/unclass/content/winter-2009-projects\">class website</a>).</p>\n<h2>1. Formation of Learning Contract</h2>\n<p>The project proposals were written by a student or a group of students, and defines what they wish to learn during the 3-4 week period of each project. Proposals can be a learning project, where students research a topic and write up a report, or a building project, where the student defines what they will create. Examples of learning projects include a <a href=\"https://island.byu.edu/unclass/wiki/jee5-learning-write\">report on Java Enterprise Edition 5</a>, or <a href=\"https://island.byu.edu/cloud-computing/wiki/amazon-web-services-overview\">wiki pages describing Amazon web services.</a> Building projects include making an <a href=\"https://island.byu.edu/unclass/content/android-web-service-app-my-weather-running-and-full-source-code\">Android application</a>, a <a href=\"https://island.byu.edu/unclass/wiki/visual-lyrics-phase-2\">lyrics visualization plug-in for iTunes</a>, or an <a href=\"https://island.byu.edu/unclass/wiki/text-messaging-sms-gateway-interactivity\">SMS gateway</a>. During the last week of each project, students start to prepare learning contracts for the next iteration.</p>\n<p>An obvious feature of the learning contract is that it is written entirely by the student. This allows for intrinsic motivation, as is illustrated in this student quote: I was able to learn a lot about and use a bunch of technologies that I never would have been able to otherwise. I feel like I learned more than in a normal class, and because I got to choose what I wanted to learn, I would usually have trouble putting down my unclass project work to take care of my other homework.</p>\n<p>The ambiguous nature of how much work should go into a contract was a source of frustration for students. One student describes the initial disorganization and lack of set expectation: We didnt have any initial standard to set our projects to. I wasnt sure if I had to build something huge to be considered complete, and I wasnt sure what was expected of the presentations. I chalk all of that up to a learning experience though. I wasnt given a ceiling so I was inclined to take my projects as far as I could, primarily because they were fun. This quote raises an interesting research question: If students (instead of teachers) are allowed to set a learning contract for a course, are the students more ambitious?</p>\n<h2>2. Students Incorporate Feedback into Proposals.</h2>\n<p>Students post their learning contract to the class website to elicit feedback from the instructor and other students. This enables other students to see and learn from the feedback from the instructor. It also allows for the possible recruitment of other students to participate in the learning project. Once the contract is revised, the instructor approves the project and the students grade is bound to the satisfactory completion of the project.</p>\n<h2>3. Contract fulfillment</h2>\n<p>Students work individually or in teams to complete their learning contracts. They are challenged with the task of finding their own learning resources, and may ask the instructor and other students for direction. This is seen as a key feature of the design. As students find their own learning resources they are able to select resources which they find most helpful. Research suggests that students and experts possess different cognitive structures (Ericsson, Charness, Feltovich, &amp; Robert R. Hoffman, 2006). Experts are able compare a new situation to what they have experienced before through pattern recognition and are then able to recognize nuances and focus on important, rather than unessential elements.</p>\n<p>Novices do not yet have a rich experience base, and as such learning is sequential (R. R. Hoffman, Shadbolt, Burton, &amp; Klein, 1995). We suggest that as students possess similar cognitive architectures they are better able to relate to novices in their class and outside of their class. As the process of learning becomes transparent and open through the use of technology students are able to use resources created by other novices to grapple with new subjects.</p>\n<h3>What resources did the students use?</h3>\n<p>If, as we suggest, students are better able to learn from learning objects created by novices (instead of experts), allowing the students to incorporate and find their own learning objects is a logical design for a course. Yet, from our experience, doing this can be very difficult for teachers. Data from the students gave surprising results on what resources were used to complete projects, and how they were found.</p>\n<p>We were surprised by how many students used social networks to find learning resources. One student remarked, Usually I would just start on a project, and when I came to the parts I didnt know as much about I would search using Google and talk to friends who I thought might be knowledgeable on the subject. As the course progressed students utilized the social network created through the class blog and wiki which was used to share knowledge that students had developed through the course of completing projects. This is illustrated through the following student comment.</p>\n<blockquote>\n<p>It was very helpful to use island and the respective blogs resulting from other students completed projects as a resource for my projects. I could remember certain things that the other class members were doing from their projects and ask them questions via instant messaging or island [the course website]. Because the class members took ownership of their projects I found them all to be very knowledgeable about the subjects they studied. This helped build a very nice knowledge base.</p>\n</blockquote>\n<p>Another student mentioned other professors and students as well as people he had met on the internet through blogs as means through which he found educational resources. Most students mentioned that they contributed to the class blog as well as other students blogs, or their own blogs.</p>\n<h2>4. Share learning deliverable</h2>\n<p>Students share what they learn in two ways. First they post it on the course website so that other students can read and comment on it. Second, they create an ignite presentation which consists of a 5 minute power point presentation of 20 slides in which the slides automatically advance every 15 seconds. This is intended to be a rapid way of transmitting ideas in an engaging and entertaining manner.</p>\n<p>By making the student learning deliverables available publicly on the Internet, it encourages student accountability for learning and creates learning objects that classmates and future students can access and build upon for their learning projects. These public traces of work are also useful as mentioned because students, by seeing what other students are working on, learn which students are expert in what and are able to use them as learning resources for those subjects. Another advantage is it helps market the class, major, and university to current and potential students. As these future students come to explore the college or university they will be able to lurk and see the projects and potential colleagues which they would be working with.</p>\n<h2>Assessment</h2>\n<p>Given the unusual nature of the class, assessment was handled differently. The majority (70%) of the points came from completely the contract as agreed upon in the learning contract. The remainder of the points were awarded for either extra work done on projects or where students helped other students in some way. This following is <a href=\"https://island.byu.edu/unclass/wiki/unclasssyllabus\">the assessment portion of the class syllabus</a>:</p>\n<ul>\n<li>70% of your grade will come from the completion of your project. Items to be graded include:</li>\n<li>Complete a contract that lists the work to be done (or learning to be achieved), the specific tasks or responsibilities of each student in the group, and any deliverables to be created.</li>\n<li>Complete an appropriately difficult project meeting the contract agreed upon</li>\n<li>Submit weekly progress updates</li>\n<li>Attendance at weekly group meetings</li>\n<li>Give an ignite presentation</li>\n<li>Attend and present at least one session at the twice per semester unconference</li>\n<li>20% of your grade will come from Learning 2.0 points. These include things such as:</li>\n<li>Reviewing and making comments on other students contracts</li>\n<li>Stories or blog posts to your project Island group</li>\n<li>Comments made on other users stories, blog posts, and comments</li>\n<li>Other indicators of interactions and work</li>\n<li>10% of your grade will come from Friend points.</li>\n<li>These are points that others can give to you (the student). They are totally at the discretion of the owner of the points. The teacher has a number of friend points he can assign, and each student has friend points he or she can give. Each student will be given 10 friends points per project. These points must be given to other students during the project or they are lost.</li>\n</ul>\n<p>Two students mentioned that the grading procedure was the worst part of the class: There was no fair assessment of the work we did. The [biggest problem with the course] was trying to figure out a grading metric for the class.</p>\n<p>As this was our first time involved with this type of course, we were unsure what would unfold. We were pleased to watch the class become a success, and see self-directed learning, and peer-teaching. Strong connections quickly formed between students which enabling them to access learning resources found throughout the community. Were sure our course design could be significantly improved but we are thrilled it was successful as it was.</p>\n<p>Ericsson, K. A., Charness, N., Feltovich, P. J., &amp; Hoffman, R. R. (2006). The Cambridge Handbook of Expertise and Expert Performance (1st ed.). Cambridge University Press.</p>\n"}

