	module.exports = {"title":"Three adoption patterns for educational social software","tags":["Education","elearning2.0","enterprise2.0","Learning","social software","organizational behavior","micro-labs"],"date":"2009-11-18T19:55:21.000Z","layout":"post","body":"<p>Ive been reading and thinking a lot lately about how to drive more adoption of the social learning platform Im building here at BYU, <a href=\"https://island.byu.edu\">https://island.byu.edu</a>, and wanted to summarize some of the highlights of what Ive learned. All of the patterns come directly from <a href=\"http://ross.typepad.com/\">Ross Mayfield</a> and <a href=\"http://michaeli.typepad.com/my_weblog/\">Michael Idinopuloss</a> writings so a big shout out to the great work theyre doing at <a href=\"http://www.socialtext.com/\">Socialtext</a>.</p>\n<h2>Pattern 1: Launch Broad then Deep</h2>\n<p>I wrote extensively about this pattern about a month ago in my post <a href=\"/deploying-social-software-universities-broad-then-deep\">Deploying Social Software in Universities: Go Broad then Deep</a>. The crux of the argument is that when deploying social software, you should first aim to get wide adoption, sign up as many people as possible, before trying to facilitate deeper, more complex engagement between your users. Social software without people is useless or as <a href=\"http://michaeli.typepad.com/my_weblog/2009/08/enterprise-20-skip-the-pilot.html\">Michael Idinopulos puts it</a>, Scale is the oxygen that feeds collaboration. In the competitive web2.0 world, the simplest tool almost always will win.</p>\n<p>Once youve achieved broad adoption, the next step is to go deep. To quote Michael again, [after achieving scale] deliver deep value by following up with local champions [deans, teachers, students] who want to invest time and effort in more robust, group-specific forms of collaboration.</p>\n<p>So launch simply and broadly. Let students and teachers adjust to having a web-enabled learning environment. Then go deep by building into the social learning platform higher-value tools such as blogs, wikis, and other collaborative learning tools and workflows.</p>\n<h2>Pattern 2: Help Learners Climb the Participation Curve</h2>\n<p>Ross Mayfield came up with a beautiful concept a number of years ago called the <a href=\"http://ross.typepad.com/blog/2006/04/power_law_of_pa.html\">Power Law of Participation</a></p>\n<p>Reproduced image:</p>\n<p><a href=\"http://michaeli.typepad.com/my_weblog/2009/09/launching-at-scale-.html\"><img src=\"./power-law-participation.jpg\" alt=\"\"></a></p>\n<p>The essence of the model is that in any community, the vast majority of the members will not have a very high level of engagement with the community. For example, in most classes there will be a few students who actively participate while the rest play solitaire. When you first deploy social learning software, most learners will only participate by reading. While they do get value from reading and lurking in communities, learners will benefit even more if they move up the participation curve and engage more deeply in some of the communities they participate in.</p>\n<p>Software adoption can be thought of in two dimensions, broad and deep. If your learners are successfully engaging in higher value / engagement tasks on your site, this deep adoption is occurring.</p>\n<h2>Pattern 3: Focus on Creating Value</h2>\n<p>Its far to easy to focus on the tools we use rather than the value were trying to create (See Jon Motts blog, <a href=\"http://www.jonmott.com/blog/2008/06/beginning-with-the-end-in-mind/\">The End in Mind</a> for an extended discussion on this subject). Adoption will increase if we focus on more creating value (<a href=\"http://www.globalnerdy.com/2009/10/17/its-about-helping-your-users-become-awesome-or-being-better-is-better-by-kathy-sierra/\">making our users awesome</a>) and less on the specific tools we are building/using.</p>\n<p>A social learning platform can add value to educational institutions in four contexts (See Michaels post <a href=\"http://michaeli.typepad.com/my_weblog/2009/04/the-social-software-value-matrix.html\">The Social Software Value Matrix</a> for more):</p>\n<ol>\n<li>Within the classroom</li>\n<li>Within departments</li>\n<li>Within the institution</li>\n<li>Across multiple institutions</li>\n</ol>\n<p>Socialtext recently released a white paper on the <a href=\"http://docs.google.com/gview?a=v&amp;q=cache:vxa4yzS7cNcJ:www.socialtext.com/documents/wp_6WaystoGetBusinessValuefromSocialSoftware.pdf+formal+informal+socialtext&amp;hl=en&amp;gl=us&amp;sig=AFQjCNFjnMdK1kI1rm0w-XW6Vxx0WlzFYQ\">6 Ways to Get Business Value from Social Software</a>. They discuss there how social software can greatly improve the formal and informal processes in each of the contexts that social software operates in.</p>\n<p>Formal processes they define as those things your organization does on a repeated basis.</p>\n<p>In an educational environment those would be things such as lectures, quizzes, studying, group projects, writing essays, exams, and so forth.</p>\n<p>Informal processes they define as all the many interactions that take place outside of this [formal] structure. They are not predictable and repeated, and therefore cannot be codified and defined Informal processes make up the bulk of [learners] interactions.</p>\n<p>In an educational environment informal processes would include such activities as informal study groups, conversations in hallways, individuals or small groups meeting with professors or TAs, forums, clubs, and so forth.</p>\n<p>Within an educational institutions there are many formal and informal learning processes that can be improved. As we focus on identifying poor processes and creating learning tools that clearly improve them, adoption of our social learning software will increase.</p>\n"}

