	module.exports = {"title":"Looking for Alpha Testers for Eduglu, a new Drupal Install Profile for Higher Education","tags":["drupal","Education","drupal planet","social learning","Drupal Install Profiles","Eduglu"],"date":"2010-02-05T18:47:56.000Z","layout":"post","body":"<p>Ive been working on a <a href=\"https://island.byu.edu\">social learning site based on Drupal</a> for the past two years as a student at Brigham Young University and am now working towards readying it for its maiden release into the world as a full-fledged Drupal install profile.</p>\n<p>My goal with <a href=\"http://eduglu.com\">Eduglu</a> is to:</p>\n<p>a) Provide a <strong>superb out-of-the-box social learning engine</strong> to help departments, clubs, classrooms, and other campus groups communicate, collaborate, and learn with one another.</p>\n<p>b) Support a <strong>social learning platform</strong> which individual universities and Drupal shops can integrate with existing tools and services and use to build custom tools supporting specific learning practices.</p>\n<p>Or in simpler terms, <strong>Eduglu = robust social learning engine + pluggable educational tools</strong>.</p>\n<p>For Drupalers, Eduglu is built on the same stack of modules as <a href=\"http://openatrium.com\">Open Atrium</a>; <a href=\"http://drupal.org/project/og\">Organic Groups</a>, <a href=\"http://drupal.org/project/spaces\">Spaces</a>, <a href=\"http://drupal.org/project/context\">Context</a>, and <a href=\"http://drupal.org/project/features\">Features</a>.</p>\n<p>Im also working to start a company to provide commercial support and hosting for Eduglu.</p>\n<p>But were not there yet. My plan is to make the first beta release by Drupalcon where <a href=\"http://sf2010.drupal.org/conference/sessions/introducing-eduglu-new-drupal-install-profile-higher-education\">Ill be presenting more about Eduglu</a>. But to be ready by then, <strong>I need alpha testers</strong>. If youre interested in becoming an alpha tester, <a href=\"mailto:mathews.kyle@gmail.com\">please contact me</a>. Ill be preparing an initial release over the next couple of weeks.</p>\n<p>Also, if you do <strong>Drupal consulting in higher education</strong>, Id love to work with you. <a href=\"mailto:mathews.kyle@gmail.com\">Please contact me</a> and lets discuss how we can work together.</p>\n<p>Lastly, if youre an <strong>educator working in higher ed</strong>, Id love to talk to you as well. Especially people working at online or virtual colleges and community colleges. If theres an educational tool youve dreamed of having  or tried building and failed  <a href=\"mailto:mathews.kyle@gmail.com\">contact me</a> and tell me about it and I might just build it for you.</p>\n<p>And if youre not in one of the above three groups but know someone who is  please connect me with those people.</p>\n<p>Ill be doing a bit of travel in a couple of weeks. Ill be in Boston February 17th-20th, New York February 22-23 and Washington DC on the 24th-25th. If youre in one of those cities and want to meet and talk Drupal/higher education, lets get in touch!</p>\n<p>Thanks all! Its going to be an exciting ride and Im looking forward to it.</p>\n"}

