	module.exports = {"title":"Notes from \"The Future of Teaching & Learning\" conference  Howard Rheingold's keynote","tags":["Education","Eduglu","future of learning","teaching","socialmediaclassroom"],"date":"2010-04-17T17:04:24.000Z","layout":"post","body":"<p>First Keynote from Howard Rheingold.</p>\n<p>Started teaching a digital journalism class then moved on to teaching digital literacy. When first started teaching classrooms, thought about how to use online media to overcome disadvantages of teaching in classrooms namely a) not all teachers good at speaking extemporously and b) classroom learning militates against thoughtfulness, cant share thought that occurs an hour after class.</p>\n<p>First started experimenting with using online tools for learning in 1995.</p>\n<p>When first started teaching in 2005? Used wikis from one tool, forums from another, blogs from another. Immediate feedback from students was this was too complex. So thought about consolidating tools. Entered Haystack competition and won some money to create the <a href=\"http://socialmediaclassroom.com\">socialmediaclassroom.com</a>.</p>\n<p>Socialmediaclassroom tool is freely downloadable or you can ask for hosted version. Hes developed a number of lesson plans for introducing participatory media to students.</p>\n<p>Developed way within Drupal to tie all work done around a lecture to be listed on page.</p>\n<p>Says forums not used as much as 10-15 years ago.</p>\n<p>Unique accordance of forums  long-lasting conversations on topics. Help students get to know each other. Continue classroom discussions asynchronously online.</p>\n<p>Introducing other features of socialmediaclassroom  blogs w/ rich media, group chat, syllabus in the wiki, social bookmarking (which is new to a lot of students)  use to collect resources for class project. Depending on class, sends students to delicious or diigo for discovery of different resources.</p>\n<p>Has found that moving classroom chairs into a circle resulting in increase in classroom conversation.</p>\n<p>Has co-teaching teams made up of students. He asks them to lists concepts and terms talked about in each class. Then other classmates fill out the descriptions.</p>\n<p>Surprised at how <em>illiterate</em> most students were when he started teaching college students.</p>\n<p>Essence of what skills needs to teach children  How can you find the answer to any question? How do you know that what you find is accurate?</p>\n<p>Example he uses  if do search for martin luther king jr The 8th result is from <a href=\"http://martinlutherking.org\">martinlutherking.org</a>  which is a website slamming on MLK. Used <a href=\"http://whoisit.com\">whoisit.com</a> to find <a href=\"http://martinlutherking.org\">martinlutherking.org</a> owned by white nationalist site Stormfront.</p>\n<p>Seems clear that youth are moving from passive consumption to active participation. Youth can create as well as consume online.</p>\n<p>Gave a number of examples of how students are using social tools to help organize collective action.</p>\n<p>We need to look at network theory, sociology research, network literacies, media theory as we redesign education for the digital age.</p>\n<p>Dont just pay attention to technology, also pay attention to literacies.</p>\n<p>Social medias are not always appropriate to teaching</p>\n<p>Not add ons, essential elements of students lives of today and tomorrow. Need to help students understand social media.</p>\n"}

