	module.exports = {"title":"Notes from \"The Future of Teaching & Learning\" conference  Tools and Technology Models","tags":["Education","education tools"],"date":"2010-04-17T19:39:34.000Z","layout":"post","body":"<p>Two questions from moderator:</p>\n<ul>\n<li>What will the next generation of learning tools look like?</li>\n<li>What do Universities need to do to prepare?</li>\n</ul>\n<p>Marko Turpeinen from Helsinki Institute for Information Technology demoing <a href=\"http://www.sizzlelab.org/\">Sizzle</a>.</p>\n<p>DrChuck warning that what works for small sizes dont scale. Too much friction to using these tech tools. There should be a one-click install.</p>\n<p>He envisions large diverse ecosystem of learning applications where teachers, grad students, small startups build learning tools that all interoperate with one another. Like the app store  teachers can find apps and click to add them depending on their need.</p>\n<p>For all this talk about next generation learning  very very few people are doing anything similar. Huge inertia. Have to build pathways into future. Lower friction to using new tools. Dr. Chuck looking at adding escape hatches into major LMSs.</p>\n"}

