	module.exports = {"title":"Second release of Eduglu","tags":["Education","drupal planet","social learning","Eduglu"],"date":"2010-06-01T21:06:28.000Z","layout":"post","body":"<h3>New release of Eduglu</h3>\n<p>Im pleased to announce the second release of <a href=\"http://eduglu.com\">Eduglu</a>! Eduglu is a Drupal distribution designed to support social learning at institutions of higher learning. Its available for download now at <a href=\"http://eduglu.com/download-eduglu\">http://eduglu.com/download-eduglu</a>. Since the first release, Weve squashed dozens of major and minor bugs and made the significant jump to the <a href=\"http://drupal.org/project/spaces\">Spaces 3</a> and <a href=\"http://drupal.org/project/context\">Context 3</a>.</p>\n<p>Eduglu helps learners connect with one another and form learning communities. Eduglu provides online spaces for groups to learn together.</p>\n<p>Our social learning platform ships with a number of powerful social learning applications including discussion boards with full email integration (like Google Groups), polls, and wikis.</p>\n<p>I recorded a quick screencast demoing an initial setup of Eduglu. Check it out to get a flavor for what Eduglu can do.</p>\n<p>\n  <object width=\"480\" height=\"385\"><param name=\"movie\" value=\"http://www.youtube.com/v/Z7HkN2W3vrI&hl=en_US&fs=1&\"></param><param name=\"allowFullScreen\" value=\"true\"></param><param name=\"allowscriptaccess\" value=\"always\"></param><embed src=\"http://www.youtube.com/v/Z7HkN2W3vrI&hl=en_US&fs=1&\" type=\"application/x-shockwave-flash\" allowscriptaccess=\"always\" allowfullscreen=\"true\" width=\"480\" height=\"385\"></embed></object>\n</p>\n<h3>Hosted Eduglu coming</h3>\n<p>Im also rather excited to announce that a fully supported hosted version of Eduglu is coming. Schools and other organizations will soon be able to easily sign-up for hosted Eduglu to meet the social learning needs of their organization. Head over to <a href=\"http://eduglu.com\">eduglu.com</a> and leave your email address to hear when we open up for business and to provide early feedback.</p>\n<p>This will be our initial stab at creating a <a href=\"http://buytaert.net/on-business-models-for-drupal-distributions\">sustainable business model for our Drupal educational distribution</a>. Theres been <a href=\"http://acquia.com/blog/drupal-software-publishing\">a</a> <a href=\"http://buytaert.net/on-business-models-for-drupal-distributions\">lot</a> <a href=\"http://developmentseed.org/blog/2010/mar/13/ideas-open-atrium-partners-program\">of</a> discussion <a href=\"http://www.agileapproach.com/blog-entry/openpublish-20-and-beyond-labor-love-1\">lately</a> in the Drupal community about finding business models that can help create sustainable Drupal Distributions. Im not sure hosted Eduglu is the right business model for that task but since a startup is <a href=\"http://steveblank.com/2010/04/12/why-startups-are-agile-and-opportunistic-%E2%80%93-pivoting-the-business-model/\">an organization formed to search for a repeatable and scalable business model</a>, well give this business model a try for awhile as we look for ways to freshen the stagnant world of educational technology.</p>\n<h3>Whats coming next for Eduglu?</h3>\n<p>Eduglu works well with 500-1000 members. But we want it to work for much much larger organizations. Organizations the size of my alma mater Brigham Young University that has 30,000 students and 250,000+ alumni. To make Eduglu work on that scale, well be adding features such as group suggesting, robust search and filtering, activity streams, and twitter-style following.</p>\n<p>Also were working on a number of social learning applications. Right now Eduglu ships with four social learning applications. A discussion/mailing list, wiki, and polling.</p>\n<p>The next two major applications wed like to ship are a blogging application that lets learners blog on various things they are learning and comment on others blogs. If the learner has an existing blog, theyll be able to connect it to their Eduglu blog so new posts there are automatically imported.</p>\n<p>The second application is a Group RSS Reader that lets a group of learners subscribe to content via RSS and provides various means for rating, sorting, sharing, and discussing content brought into the group.</p>\n<p>Both are powerful learning tools that help students more easily share what they are learning and track and discuss the best ideas that are being written about on the Internet and were excited to get them out into the wild.</p>\n"}

