	module.exports = {"title":"First beta release for Drupal-native mailinglist module","tags":["drupal","drupal planet","Eduglu","OG Mailinglist"],"date":"2010-09-15T22:49:54.000Z","layout":"post","body":"<p>Today I rolled out the first beta for my <a href=\"http://drupal.org/project/og_mailinglist\">Drupal module, OG Mailinglist</a>, which provides native mailinglist functionality to Drupal similar to <a href=\"http://www.gnu.org/software/mailman/index.html\">Mailman</a> or <a href=\"http://groups.google.com\">Google Groups</a>.</p>\n<p>OG Mailinglist provides complete email integration for Organic Group-based Drupal sites. Many people find it easier to participate in online conversations through email rather than having to visit a website continually. OG Mailinglist provides this email integration for your site. <a href=\"http://eduglu.com\">My company</a> is supporting the development of this module as part of our development of <a href=\"http://eduglu.com\">Eduglu, a Drupal-based social learning platform</a>.</p>\n<p>OG Mailinglist was started as a student project at BYU for the site I built there, <a href=\"http://island.byu.edu\">http://island.byu.edu</a>. Since its creation, OG Mailinglist has successfully supported 1000s of discussions.</p>\n<h3>Features</h3>\n<p>OG Mailinglist has a rich array of features. Current features include:</p>\n<ul>\n<li>Full integration with Organic Groups.</li>\n<li>Each group has its own email address.</li>\n<li>You can create and reply to posts via email as well as through the web site.</li>\n<li>Full HTML Digests. They are loosely modeled after Google Groups digest emails and are very readable.</li>\n<li>Emails include the list-id header to make filtering easy in your email client.</li>\n<li>Low latency. Emails arriving at your site are resent immediately instead of waiting for the next cron run. Other group members will typically receive your email within one minute of you sending it. This can greatly speed up group conversations on time sensitive issues.</li>\n</ul>\n<h3>Requirements</h3>\n<p>Installing and configuring this module requires root access and fairly advanced sysadmin skills. Right now, there is instructions for integrating with Exim4 and Qmail. Theres already efforts at way to support Postfix, so <a href=\"http://drupal.org/node/771704\">please join in on this issue if you have Postfix expertise</a>.</p>\n<p>OG Mailinglist has come a long long way since the project started 1.5 years ago but theres still <a href=\"http://drupal.org/project/issues/og_mailinglist\">plenty of bugs to share</a>. So please pitch in and help make this module even more awesome.</p>\n"}

