	module.exports = {"title":"Eduglu, Roadmaps, platforms, and killer apps","tags":["drupal","drupal planet","Eduglu","roadmap","platform","killer apps","apps"],"date":"2010-11-04T23:10:52.000Z","layout":"post","body":"<p>Killer platforms need killer apps. A great platform by itself isnt that interesting or useful. It takes killer apps to attract customers and define the platform.</p>\n<p>Since the computer industry hit its stride weve seen a number of examples of killer platforms and killer app duos. 1000s of financial workers bought the Apple II platform on the the strength of VisiCalc and Lotus 1-2-3. Once Facebook opened up their platform, 1000s of applications and services were written, most worthless, but social gaming company Zyanga has made 100s of millions of dollars from games like Farmville and Mafia Wars. Apples App Store has introduced millions to the app lifestyle through its various iOS devices.</p>\n<p>In the world of the Drupal platform, many people think Drupal distros are becoming the killer apps of Drupal. They (and I) believe that these distros / applications will help Drupal attract new customers in new markets. Popular distros, such as <a href=\"http://openatrium.com\">Open Atrium</a> and <a href=\"http://openpublishapp.com/\">OpenPublish</a>, are starting to show that this model can work as they and other distros are receiving attention far outside the normal channels and communities which discuss Drupal.</p>\n<p>For the past year or so, Ive been working on building a Drupal distro called <a href=\"http://eduglu.com\">Eduglu</a>, which is a social learning product designed to support groups of learners. Similar to Drupal, my companys product Eduglu is a platform. As part of developing the platform Ive built a number of apps for it such as a forum/mailinglist, polls, etc.</p>\n<p>But while these are nice apps and Im really pleased about how the platform is coming together, I feel that Eduglu is still missing its set of killer apps.</p>\n<p>Many people look at Eduglu now and think meh, what does it do, they ask, that existing education / community platforms dont already do?</p>\n<p>And I cant really answer that question. Not yet anyways. Eduglu is still missing its killer app that for 1000s of organizations will become <em>the</em> reason that they <em>must</em> adapt Eduglu as their learning platform. Something like email that for millions of companies and households was the reason they signed up for Internet.</p>\n<p>So recently, Ive made a prediction to myself, that Eduglu will become a success only if a large number of apps are built on top of the platform.</p>\n<p>But luckily, I dont have to rely entirely on people building apps specifically for Eduglu. In <a href=\"http://buytaert.net/\">Dries Buytaerts</a> (founder of Drupal) <a href=\"http://buytaert.net/drupal-distributions\">first post on Drupal distributions</a> four years ago, he said that for Drupal distributions to succeed, they must collaborate not compete. That Drupal distros cant fork Drupal core or otherwise introduce incompatibilities between themselves.</p>\n<p>And happily, that is what has happened. Through the efforts of many in the Drupal community, particularly <a href=\"http://developmentseed.org/\">Development Seed</a>, it is possible to take an application written for one Drupal distro and reuse it across multiple other distros. And one very interesting effort, <a href=\"http://drupal.org/project/debut\">Debut</a>, is working to build a common set of applications specifically designed to serve as base applications for a variety of Drupal distrubutions.</p>\n<p>So this has left me, Eduglu, and other Drupal distros in a nice spot. Many people are working together to build reusable apps and Drupal, particularly with the upcoming Drupal 7 release, has developed into a very nice framework for building web applications.</p>\n<p>So I have two items now on my roadmap for Eduglu.</p>\n<ol>\n<li>Refine and evangelize the platform and</li>\n<li>Develop killer apps.</li>\n</ol>\n<p>And while predicting in advance what is or isnt a killer app is difficult, I have a few ideas up my sleeve. First on the list is a realtime notetaking app built with Drupal and <a href=\"http://etherpad.org\">Etherpad</a>. Students will be able to take notes together, attach audio recordings of the lecture and other media files, and save all of this to the class website where itll it can be referred to in the future.</p>\n<p>And if you already know what youre killer app for learning is and just need someone to build it then by all means, <a href=\"http://eduglu.com/contact\">please contact us!</a> Wed be happy to help :)</p>\n"}

