	module.exports = {"title":"Creating a CCK Field module with an \"invisible\" widget","tags":["drupal","drupal planet","etherpad","drupal modules"],"date":"2010-11-19T00:14:48.000Z","layout":"post","body":"<p>Most CCK Field modules have a widget where the user adds information upon creating a node which is then saved with the node. For the recent <a href=\"http://drupal.org/project/etherpad\">Etherpad module</a> I wrote, I needed an invisible widget which saved with each new node some information from the field definition as well as autogenerated information. As I didnt any documentation on how to do this, I thought Id document it here quickly.</p>\n<p>The first thing you do is define your database columns for your field in hook_field_settings.</p>\n<pre><code class=\"language-php\"><span class=\"hljs-meta\">&lt;?php</span>\n<span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> <span class=\"hljs-title\">etherpad_field_settings</span><span class=\"hljs-params\">($op, $field)</span> </span>{\n  <span class=\"hljs-keyword\">switch</span> ($op) {\n    <span class=\"hljs-comment\">// Code removed.</span>\n    <span class=\"hljs-keyword\">case</span> <span class=\"hljs-string\">'database columns'</span>:\n      <span class=\"hljs-keyword\">return</span> <span class=\"hljs-keyword\">array</span>(\n        <span class=\"hljs-string\">'etherpad_url'</span> =&gt; <span class=\"hljs-keyword\">array</span>(<span class=\"hljs-string\">'type'</span> =&gt; <span class=\"hljs-string\">'varchar'</span>, <span class=\"hljs-string\">'length'</span> =&gt; <span class=\"hljs-number\">1024</span>, <span class=\"hljs-string\">'not null'</span> =&gt; <span class=\"hljs-keyword\">FALSE</span>,),\n        <span class=\"hljs-string\">'etherpad_text'</span> =&gt; <span class=\"hljs-keyword\">array</span>(<span class=\"hljs-string\">'type'</span> =&gt; <span class=\"hljs-string\">'text'</span>, <span class=\"hljs-string\">'not null'</span> =&gt; <span class=\"hljs-keyword\">TRUE</span>, <span class=\"hljs-string\">'size'</span> =&gt; <span class=\"hljs-string\">'big'</span>),\n        <span class=\"hljs-string\">'attributes'</span> =&gt; <span class=\"hljs-keyword\">array</span>(<span class=\"hljs-string\">'type'</span> =&gt; <span class=\"hljs-string\">'text'</span>, <span class=\"hljs-string\">'size'</span> =&gt; <span class=\"hljs-string\">'medium'</span>, <span class=\"hljs-string\">'not null'</span> =&gt; <span class=\"hljs-keyword\">FALSE</span>),\n      );\n  }\n}\n<span class=\"hljs-meta\">?&gt;</span>\n</code></pre>\n<p>Next, you define your widget form inside hook_widget. Some tutorials I saw suggest you define your widget form in hook_elements/hook_process. I did that at first but decided against it as a) I never got it to work and b) it just adds needless complexity. Generally youll just want to define your widget in hook_widgets.</p>\n<p>Two really important things here to get your invisible widget to work correctly. First, you <strong>must</strong> name your form keys the same as you named your database column names in hook_field_settings. This tripped me up for a long time. CCK saves data by magic (you never explicitly save anything from a widget) and this is the key to getting the incantation to take. Second, using the &quot;<a href=\"http://api.drupal.org/api/drupal/developer--topics--forms_api_reference.html/6#val\">value&quot; field type</a> was the key to creating an invisible field and getting my data saved correctly.</p>\n<pre><code class=\"language-php\"><span class=\"hljs-meta\">&lt;?php</span>\n<span class=\"hljs-comment\">/**\n * Implementation of hook_widget().\n */</span>\n<span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> <span class=\"hljs-title\">etherpad_widget</span><span class=\"hljs-params\">(&amp;$form, &amp;$form_state, $field, $items, $delta = <span class=\"hljs-number\">0</span>)</span> </span>{\n  $element[<span class=\"hljs-string\">'etherpad_url'</span>] = <span class=\"hljs-keyword\">array</span>(\n    <span class=\"hljs-string\">'#type'</span> =&gt; <span class=\"hljs-string\">'value'</span>,\n    <span class=\"hljs-string\">'#value'</span> =&gt; (<span class=\"hljs-keyword\">isset</span>($items[$delta][<span class=\"hljs-string\">'etherpad_url'</span>]) &amp;&amp; !<span class=\"hljs-keyword\">empty</span>($form[<span class=\"hljs-string\">'nid'</span>][<span class=\"hljs-string\">'#value'</span>])) ? $items[$delta][<span class=\"hljs-string\">'etherpad_url'</span>] : $field[<span class=\"hljs-string\">'etherpad_url'</span>] . etherpad_generate_padid($field[<span class=\"hljs-string\">'etherpad_url'</span>]),\n  );\n  $element[<span class=\"hljs-string\">'etherpad_text'</span>] = <span class=\"hljs-keyword\">array</span>(\n    <span class=\"hljs-string\">'#type'</span> =&gt; <span class=\"hljs-string\">'value'</span>,\n    <span class=\"hljs-string\">'#value'</span> =&gt; (<span class=\"hljs-keyword\">isset</span>($items[$delta][<span class=\"hljs-string\">'etherpad_text'</span>]) &amp;&amp; !<span class=\"hljs-keyword\">empty</span>($form[<span class=\"hljs-string\">'nid'</span>][<span class=\"hljs-string\">'#value'</span>])) ? $items[$delta][<span class=\"hljs-string\">'etherpad_text'</span>] : <span class=\"hljs-string\">\"default value for now until we have a function to generate one\"</span>,\n  );\n  $element[<span class=\"hljs-string\">'attributes'</span>] = <span class=\"hljs-keyword\">array</span>(\n    <span class=\"hljs-string\">'#type'</span> =&gt; <span class=\"hljs-string\">'value'</span>,\n    <span class=\"hljs-string\">'#value'</span> =&gt; (<span class=\"hljs-keyword\">isset</span>($items[$delta][<span class=\"hljs-string\">'attributes'</span>]) &amp;&amp; !<span class=\"hljs-keyword\">empty</span>($form[<span class=\"hljs-string\">'nid'</span>][<span class=\"hljs-string\">'#value'</span>])) ? $items[$delta][<span class=\"hljs-string\">'attributes'</span>] : serialize($field[<span class=\"hljs-string\">'attributes'</span>]),\n  );\n  <span class=\"hljs-comment\">// Used so that hook_field('validate') knows where to</span>\n  <span class=\"hljs-comment\">// flag an error in deeply nested forms.</span>\n  <span class=\"hljs-keyword\">if</span> (<span class=\"hljs-keyword\">empty</span>($form[<span class=\"hljs-string\">'#parents'</span>])) {\n    $form[<span class=\"hljs-string\">'#parents'</span>] = <span class=\"hljs-keyword\">array</span>();\n  }\n  $element[<span class=\"hljs-string\">'_error_element'</span>] = <span class=\"hljs-keyword\">array</span>(\n    <span class=\"hljs-string\">'#type'</span> =&gt; <span class=\"hljs-string\">'value'</span>,\n    <span class=\"hljs-string\">'#value'</span> =&gt; implode(<span class=\"hljs-string\">']['</span>, array_merge($form[<span class=\"hljs-string\">'#parents'</span>], <span class=\"hljs-keyword\">array</span>(<span class=\"hljs-string\">'value'</span>))),\n  );\n\n  <span class=\"hljs-keyword\">return</span> $element;\n}\n<span class=\"hljs-meta\">?&gt;</span>\n</code></pre>\n<p>And thats it! Read this and youll save yourself hours of frustration :)</p>\n<p>One other note, the Devel modules reinstall module function is very useful as youll be reinstalling the module often to reset the database w/ your changes. Enable the Devel block to access it.</p>\n<p>This documentation doesnt cover most of what youll need to know to write a CCK module. I relied heavily on the following tutorials.</p>\n<ul>\n<li><a href=\"http://maxeydevbox.org/blogs/geoffmaxey/building-custom-compound-fieldswidgets-cck-drupal-6x\">http://maxeydevbox.org/blogs/geoffmaxey/building-custom-compound-fieldswidgets-cck-drupal-6x</a></li>\n<li><a href=\"http://www.lullabot.com/articles/creating-custom-cck-fields\">http://www.lullabot.com/articles/creating-custom-cck-fields</a></li>\n<li><a href=\"http://www.poplarware.com/articles/cck_field_module\">http://www.poplarware.com/articles/cck_field_module</a></li>\n</ul>\n"}

