	module.exports = {"title":"Eduglu enters the deadpool","tags":null,"date":"2011-04-22T23:03:50.000Z","layout":"post","body":"<p>I just posted this on the <a href=\"http://community.eduglu.org/development/node/185\">Eduglu community site</a>. Reposted here without further comment:</p>\n<blockquote>\n<p>It is with mixed emotions that I announce that Im quitting work on Eduglu effective immediately. Ive greatly enjoyed the time Ive spent working on Eduglu and the friendships Ive formed in person and online. I had high hopes, shared with many, in the promise of open source social learning tools but I was never able to find a business model that would make my and your work sustainable.</p>\n<p>A few other notes. This community site and <a href=\"http://eduglu.com\">eduglu.com</a> Ill keep up and maintain for the forseeable future. All of Eduglus source code is, of course, open source and hosted on Github and <a href=\"http://Drupal.org\">Drupal.org</a> so if youd like to continue using/maintaining it, feel free.</p>\n<p>Now that Im not working on Eduglu Im looking for work. If you have paid part-time gigs available, send me an email. Im interested in Drupal work of course but Id be open to about anything you think Im qualified for.</p>\n<p>Hopefully this isnt a good bye and that our paths pass again in the future.</p>\n</blockquote>\n"}

