	module.exports = {"title":"Becoming unhinged from realityand fixing that","tags":["john boyd","creative destruction","reality"],"date":"2011-05-10T20:01:57.000Z","layout":"post","body":"<p>This is from <a href=\"http://www.amazon.com/Boyd-Fighter-Pilot-Who-Changed/dp/0316796883/ref=sr_1_3?s=books&amp;ie=UTF8&amp;qid=1305057501&amp;sr=1-3\">a most excellent biography of a most interesting person, John Boyd</a>.</p>\n<blockquote>\n<p>The danger is that if our mental processes become focused on our internal dogmas and isolated from the unfolding, constantly dynamic outside world, we experience mismatches between our mental images and reality. Then confusion and disorder and uncertainty not only result but continue to increase. Ultimately, as disorder increases, chaos can result. Boyd showed why this is a natural process and why the only alternative is to do a destructive deduction and rebuild ones mental image to correspond to the new reality.</p>\n</blockquote>\n<p>Also related is the idea of creative destruction. You can find a good <a href=\"http://www.ribbonfarm.com/2008/02/06/creative-destruction-portrait-of-an-idea/\">discussion/history of the idea over at Ribbonfarm.com</a>.</p>\n"}

