	module.exports = {"title":"How to create a rsum using Latex","tags":null,"date":"2011-05-21T02:22:02.000Z","layout":"post","body":"<p>Since <a href=\"/eduglu-enters-deadpool/\">recent changes in my life</a> have forced me to look for new employment, Ive had to relearn the long-forgotten art of writing a rsum. Since the actual process of writing a rsum is fairly straightforward, and boring, I decided to spice things up by figuring out how to do it in <a href=\"http://en.wikipedia.org/wiki/LaTeX\">Latex</a>, a recent hobby of mine.</p>\n<p>Googling found quite a few different Latex rsum templates to start from and my favorite by far was <a href=\"http://kjhealy.github.com/kjh-vita/\">this one</a>, conveniently already on Github. So I quickly forked the example rsum and modified it to fit my needs. I was very happy how it turned out and think it looks miles better than any previous Microsoft Word template rsum Ive produced.</p>\n<p><a href=\"./kyle-mathews-resume_0.pdf\">Download a copy of the finished rsum</a>. You can also see the source for the rsum at <a href=\"https://github.com/KyleAMathews/kjh-vita/blob/master/kyle-resume.tex\">https://github.com/KyleAMathews/kjh-vita/blob/master/kyle-resume.tex</a>. Feel free to fork my version and modify to meet your needs. Most distributions of Latex should have all the dependencies you need. The three fonts Ive used, Minion Pro, Myriad Pro, and Inconsolata are all freely available. The first two are distributed with Adobe Reader. The latter can be <a href=\"http://www.levien.com/type/myfonts/inconsolata.html\">downloaded here.</a></p>\n<h3>Update  just use <a href=\"http://sharelatex.com\">http://sharelatex.com</a></h3>\n<p>Since I wrote this post several years ago a new online Latex service <a href=\"http://sharelatex.com\">Sharelatex</a> has been created. They have a fantastic selection of <a href=\"https://www.sharelatex.com/templates/cv-or-resume\">Resume/CV Latex templates</a> and couldnt be easier to use. Just <a href=\"https://www.sharelatex.com/templates/cv-or-resume\">pick one of the templates</a> and start adding your information through their online editor.</p>\n"}

