	module.exports = {"title":"SimpleGTD","tags":["gtd"],"date":"2011-10-07T18:59:39.000Z","layout":"post","body":"<p>Im starting work soon on building a new open source <a href=\"http://en.wikipedia.org/wiki/Getting_Things_Done\">Getting Things Done</a> web app that Im tentatively calling <a href=\"https://github.com/KyleAMathews/makersgtd\">SimpleGTD</a>. Ive tried a good chunk of the GTD apps out there and have been dissatisfied with each of them for one reason or another.</p>\n<h2>Motivations</h2>\n<p>I have a few reasons for starting this project.</p>\n<p>First is I really like the GTD system. When its running well, I really do feel more organized and able to focus on my priorities. But Ive never been able to find a system that was maintainable long-term. Ive tried many that worked for a time but each gradually required more and more effort to maintain until eventually I had to quit using them. Ive thought a fair bit about why theyve failed and I believe I can create an app that will actually work long-term.</p>\n<p>My second motivation is theres a number of really cool technologies Id like to learn or practice with including Node.js, Coffeescript, Backbone.js, ElasticSearch, Responsive Design, AppCache, and LocalStorageeach of which Ill be using to build SimpleGTD. Its really hard to learn new skills outside of the context of working toward a concrete goal so a nice compact project like this is gold.</p>\n<p>My third motivation is to keep my startup muscles strong. For a variety of reasons, Im not interested in doing another startup right now. But I will in the future. I plan to offer hosting and support for SimpleGTD app (for those not wanting to host it themselves). Doing the marketing, handling payments, support issues, etc. will allow me to practice all the parts of running a real company without the stress of caring whether my micro-business actually succeeds.</p>\n<p>I have a number of similar side projects planned or in progress that Ill also be treating as micro-businesses. Im building these projects primarily for myself but itll be fun to also put them out in the wild and see what happens.</p>\n<h2>Key design principles</h2>\n<h3>Fast</h3>\n<p><a href=\"http://thinkvitamin.com/web-apps/fred-wilsons-10-golden-principles-of-successful-web-apps/\">To quote Fred Wilson</a>, Speed is more than a feature. Speed is the most important feature. If your application is slow, people wont use it.</p>\n<p>More than any other flaw, lack of speed is the reason I eventually have quit the software GTD apps Ive tried. Paper is really fast. Compared to a piece of paper or sticky note sitting on your desk, its cumbersome to have to load a desktop or web app just to view your tasks.</p>\n<p>To compete with paper, any software-based GTD system must load instantly and quickly show you exactly what you want.</p>\n<p>Other actions, such as navigating, adding, and editing projects and actions must also be fast. My plan is that all movement within the application can be done via the keyboard, ala GMail.</p>\n<p>To meet my speed standards, Im going to use a variety of techniques. First all code and data will be stored locally using AppCache and LocalStorage. The app will load first from LocalStorage and only then sync with the server. This should solve much of the slow-start problem.</p>\n<p>Second, Ill build the app as a single-page app. All communication with the server will happen asynchronously. This means viewing a project page or adding a new task wont require reloading the page so navigation within the app will be fast.</p>\n<h3>Simple</h3>\n<p>A big problem Ive had with other systems Ive tried is theyre too complex for my tastes. They try too hard. I dont want to implement <a href=\"http://www.dragosroua.com/staying-gtd-over-the-hype/\">every idea David Allen has ever come up with</a>. All I want my GTD app to do is add projects, add action items to these projects, and easily choose and sort the actions that Ill be working on the next day or two.</p>\n<p>Paper-like simplicity is my goal here, not Microsoft Project.</p>\n<h3>Built for the 21st century</h3>\n<p>This is the 21st century. Almost everyone now is carrying around a smartphone in their pocket 24/7. No one prints off todos anymore. You should be able to select your action items for the day on your computer, walk out the door, open the app on your phone and instantly see what needs to be done.</p>\n<h2>Built to forgive and protect</h2>\n<p>The above ideas are fairly standard. My last idea is a bit more unique.</p>\n<p>Keeping task lists is a hard discipline and <a href=\"https://plus.google.com/114723964985237592593/posts/hsVEEpov6Ti\">almost everyone fails at times</a>. Our tasks lists seem to quickly disintegrate into a disorganized, unwieldy mess without the occasional superhuman effort to clean things up. Id like SimpleGTD to help us avoid that superhuman effort.</p>\n<p>I think this is one reason I like paper. When a list of tasks on paper gets too overwhelming, you can just throw it away or bury it under other paper. Most GTD/Task software is less forgiving in this respect. If you add a project or task its there forever until you explicitly delete it.</p>\n<p>The problem is our brains has a selective membrane of sorts for commitments. It requires little force to insert a commitment but quite a bit of force to remove it. Were addicted to being productive and the rewards that come from completing tasks. With each added task, we imagine the praise well get once we accomplish it. Each new task is a shot of dopamine. Deleting a task or project kills the hope for that future reward.</p>\n<p>In my past forays with GTD software, I would cheerful add tasks and projects much faster than I could finish them. They would accumulate and accumulate until I dreaded even looking at the software with its long list of uncompleted tasks.</p>\n<p>What we have is a garbage collection problem. We accumulate projects and tasks dont throw out the ones weve abandoned. The garbage piles up higher and higher until eventually we have to abandon the house altogether and start a new one.</p>\n<p>Like people portrayed on the TV show <a href=\"http://www.aetv.com/hoarders/about/\">Hoarders</a>, we find it difficult to throw away commitments.</p>\n<p>I think the weekly review is whats supposed to corral this problem but Ive never been able to do one consistently  most likely because I dread having to decide what projects/tasks to abandon.</p>\n<p>So what we need is to have the software help us to delete projects/tasks that we cant accomplish. As Ive proven many-a-time, I cant manually keep my tasks lists in check. What we need I think is a Roomba-like garbage collector that detects dying projects and cleans them up for us.</p>\n<p>But how to detect those projects/tasks that have gone stalethose projects/tasks that weve effectively abandoned but havent yet told our consciousness? How can the software pick up on our unconscious cues that a task or project should be removed?</p>\n<p>The best way Ive thought of so far is to track how often we look at something. If were actually working on a project or task, well be viewing or editing it fairly often. When we start to abandon a project or task, we stop looking at it. The software will track each time a project or task is viewed. If enough time passes since the last time you viewed the project, itll be garbage collected and removed from active view. For tasks, theyll be removed from the active task list. For projects, theyll be moved to the someday/maybe list. And for projects already in the someday/maybe list, eventually theyll be moved into an archived list.</p>\n<p>On timing, my thinking is tasks get garbage collected after 24 hours, projects after two months, and someday/maybe projects after six months.</p>\n<p>So if you want a task or project to disappear, just ignore it and itll eventually go away. Which follows our natural pattern. If we dont want to do something, we stop thinking about and eventually forget it. Most software has the annoying property of never forgetting.</p>\n<p>At the start of the week, your active todos list should be white and empty, waiting for you to select the weeks tasks. If you load up a number of tasks at the beginning of the week but an emergency comes up, those tasks will silently disappear letting you focus on the emergency. If youre heavily focused for a few months on a couple projects at work, all our other projects will kindly hide themselves in someday/maybe. If we abandon the GTD-ship in despair, when we come back, all our projects will be waiting in storage and easily reactivated.</p>\n<h2>Moving forward</h2>\n<p>Im really excited about my plans. It should be a fun project technically and is software I could really use right now.</p>\n<h3>How you can get involved</h3>\n<p>Id appreciate feedback on my ideas. If youve tried and failed with GTD in the past, why did you fail? How did the system you used (software or otherwise) help/hinder you? Did my analysis, that failure comes from an overload of low-priority projects/tasks, resonate with you? Am I missing anything obvious here?</p>\n<p>Once I get a usable prototype built, Id love beta testers. Please contact me if youre interested in getting early access.</p>\n"}

