	module.exports = {"title":"Building your own tools","tags":["tools"],"date":"2011-10-28T08:00:37.000Z","layout":"post","readNext":"/problem-too-general-tools/","body":"<p>I was explaining to someone in IRC earlier this week about why I was <a href=\"/simplegtd\">building my own project management software</a> and said it was basically because managing my projects and tasks (or more abstractly, what I pay attention to) is so critical to everything else I do that I want to control the software behind it.</p>\n<p>Afterwards, that struck me as a rather important point. After some further thinking, my point is fairly close to the common business question of whether a particular service should be outsourced or kept in-house. Theres a lot that goes into making that decision but one pretty hard-and-fast rule is you never outsource a core competency.</p>\n<p>I realized that in persisting in using poor-fitting tools for managing my attention, I had to some extent been outsourcing my core competency as a knowledge worker, i.e. defining what my work is and helping focus my attention. My ability to effectively do these two tasks has been hindered by using poor quality and poor fitting tools. <a href=\"http://en.wikiquote.org/wiki/Marshall_McLuhan\">As Marshall Mcluhan said</a>, we shape our tools and thereafter our tools shape us. Tools can be powerful inhibitors or powerful enablers in all sorts of subtle and unexpected ways.</p>\n<p>The last few weeks Ive started paying much closer attention to the quality and fit of my intellectual tools and if I should bring any of their creation in-house and recreate them to meet my particular needs. Which is a tricky thing to decide. You dont want, in a fit of vainglory, to go off and try to recreate <a href=\"http://en.wikipedia.org/wiki/Vim_(text_editor)\">Vim</a> badly. I think in addition to my project management tool, Ill be building a few writing tools. Beyond that Ill wait to see how these first few experiments go.</p>\n<p>What tools, intellectual or otherwise, have you built and how well did they work?</p>\n"}

