	module.exports = {"title":"New Beginnings","date":"2014-04-16T22:40:32.169Z","layout":"post","readNext":"/hosting-static-sites-with-docker-and-nginx/","body":"<p>Quick announcement post.</p>\n<p>First Im leaving <a href=\"http://getpantheon.com\">Pantheon</a>, the startup Ive been working at the past two years.\nIts been a fantastic run and Ive loved working there. They have a great\nteam and product and its been a pleasure helping build the best Drupal (and now Wordpress)\nprofessional website platform.</p>\n<p>Highlights include helping with:</p>\n<ul>\n<li>Rebuilding the Pantheon dashboard from the ground up as a real-time single-page-app.</li>\n<li><a href=\"http://techcrunch.com/2013/07/10/pantheon-multidev/\">Multidev</a>cloud development environments for teams.\nBasically Git for the entire stack (files, DB, and code) not just code. Its been a huge hit for customers with larger teams of developers.</li>\n<li><a href=\"https://www.getpantheon.com/pantheon-one\">Pantheon One</a>a suite of tools for customers administering 100s to 1000s of websites.</li>\n</ul>\n<p>Now that Ive left Pantheon, for the indefinite future Ill be freelancing and working my way through a long list\nof side-projects and books that have been accumulating. <a href=\"mailto:mathews.kyle@gmail.com\">Contact me</a> if you have interesting\nproblems youd like help with.</p>\n<p>Im also back in the blogging game. I rebuilt this blog recently as a static site with a custom theme and new domain name. Theres a number of topics, technical and otherwise, Ill be exploring here in future months.</p>\n"}

